import { FeatureAuthService } from './../../../shared/services/feature-access.service';
import { AfterViewInit, Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { Script } from '../../../shared/services/script.service';
import { MembershipService } from '../../../shared/services/membership.service';
import { Estimate } from '../../../shared/models/estimate';
import { CurrentCompany } from '../../../shared/models/company';
import { SubDomainService } from '../../../shared/services/subdomain.service';
import { CookieService } from '../../../shared/services/cookie.service';

declare var jQuery: any;
declare var window: any;
declare var ga: any;
declare var fbq: any;

@Component({
  selector: 'app-membership-new',
  templateUrl: './membership-new.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./membership-new.component.css', './../../../../assets/css/custom-material.css']
})
export class MembershipNewComponent implements OnInit, AfterViewInit {
  currentCompany: any;
  currentPlan: any;
  cardSetupForm: FormGroup;
  card: any;
  cardNum: any;
  error: any;
  upgPlan: any;
  cancel_coupon: boolean;
  couponOffer: any;
  estimation: Estimate;
  preloader: boolean;
  appliedCoupon: string;
  planItems;
  cardStatus: any;
  isChangePlan: boolean;
  upgradeTo: any;
  couponForm: FormGroup;
  modalOpened: number;
  saveClicked: boolean = false;
  userRole: string;
  disc20 = false;
  isPlanChangedToMonthly: boolean;
  constructor(private title: Title,
    private subdomainService: SubDomainService,
    public membershipService: MembershipService,
    private script: Script,
    private cookieService: CookieService,
    public _featureAuthService: FeatureAuthService) {
    this.currentCompany = null;
    this.currentPlan = null;
    this.error = {
      status: false,
      message: null
    };
    this.upgPlan = null;
    this.cancel_coupon = false;
    this.preloader = false;
    this.appliedCoupon = null;
    this.planItems = [];
    this.isChangePlan = false;
    this.modalOpened = 0;
    this.isPlanChangedToMonthly = false;
  }

  ngOnInit() {
    this.userRole = this.cookieService.readCookie('role');
    let status = JSON.parse(this.cookieService.readCookie('status'));
    // if(this.userRole === 'MANAGER' && status.subsStatus != 'cancelled') {
    //   window.location.href = environment.PROTOCOL + this.subdomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/dashboard/';
    //   return;
    // } else {
    const self = this;
    this.title.setTitle('Outgrow Membership');
    this.currentCompany = this.subdomainService.currentCompany;
    this.membershipService.getCurrentPlan().subscribe((CurrentPlan) => {
      this.currentPlan = CurrentPlan;
    });
    this._featureAuthService.getPlanFeatures().subscribe((PlanItems) => {
      if (!jQuery.isEmptyObject(PlanItems)) {
        this.planItems = this.membershipService.setPlanItems(PlanItems);
        if (this.currentCompany.deal_refered === 'JVZOO' || this.currentCompany.deal_refered === 'PKS') {
          const filterPlan = this.planItems.filter((plan) => {
            if (this.currentCompany.billing.chargebee_plan_id.indexOf('standard') !== -1) {
              if (plan._id.indexOf('jv') !== -1) {
                return plan;
              }
            } else {
              if (plan._id === 'business_jv') {
                return plan;
              }
            }
          });
          this.planItems = filterPlan;
        } else if (this.currentCompany.is_appsumo_created) {
          const filterPlan = this.planItems.filter((plan) => {
            if (plan._id !== 'freelancer' && plan._id.indexOf('jv') === -1) {
              return plan;
            }
          });
          this.planItems = filterPlan;
        } else {
          const filterPlan = this.planItems.filter((plan) => {
            if (plan._id !== 'enterprise' && plan._id.indexOf('jv') === -1) {
              return plan;
            }
          });
          this.planItems = filterPlan;
        }
        this.planItems.sort((obj1, obj2) => {
          const key1 = obj1.cbPlan[0].price, key2 = obj2.cbPlan[0].price;
          if (key1 < key2) {
            return -1;
          }
          if (key1 > key2) {
            return 1;
          }
          return 0;
        });
      }
    });
    this.membershipService.getBillingDetail().subscribe((success: any) => {
      this.cardStatus = success.card ? success.card.status : null;
    });
    this.initializeForm();

    jQuery(document).on('show.bs.modal', '.modal', function (event) {
      self.modalOpened++;
    });

    jQuery(document).on('hidden.bs.modal', function (event) {
      self.modalOpened--;
      // self.membershipService.clearAllIntervals();
      if (self.modalOpened <= 0) {
        jQuery('body').removeClass('modal-open');
      }
    });
    jQuery("#cc-modal-new").on('hidden.bs.modal', (event) => {
      // if(this.cardSetupForm.invalid || !this.saveClicked){
      //   let message = `Did not Setup card`;
      //   this.membershipService.billingPageNotifier({message,
      //     company:`${this.currentCompany.id}(${this.currentCompany.sub_domain})`
      //   }).subscribe((data)=>{
      //     console.log("data",data);
      //   });
      // }
    });
    // }
  }

  ngAfterViewInit() {
    let self = this;
    let hb = JSON.parse(this.cookieService.readCookie('hellobar'));
    if (hb) {
      if (hb.message.indexOf('Get 20% off on all plans') !== -1) {
        this.disc20 = true;
      }
    }
    jQuery.material.init();
    let interval = setInterval(function () {
      if (window.Intercom) {
        self.userRole = self.cookieService.readCookie('role');
        self.currentCompany = self.subdomainService.currentCompany;
        window.Intercom('update', { 'app_current_page': 'membership' });
        window.Intercom('update', { 'app_current_page_url': window.location.href });
        clearInterval(interval);
      }
    }, 1000);
  }

  initializeForm() {
    this.isChangePlan = false;
    this.card = null;
    this.cardSetupForm = new FormGroup({
      cardNumber1: new FormControl('', [Validators.required, Validators.minLength(4), Validators.pattern('^[0-9]*$')]),
      cardNumber2: new FormControl('', [Validators.required, Validators.minLength(4), Validators.pattern('^[0-9]*$')]),
      cardNumber3: new FormControl('', [Validators.required, Validators.minLength(4), Validators.pattern('^[0-9]*$')]),
      cardNumber4: new FormControl('', [Validators.pattern('^[0-9]*$')]),
      nameOnCard: new FormControl('', [Validators.required, Validators.minLength(3)]),
      cvv: new FormControl('', [Validators.required, Validators.minLength(3), Validators.maxLength(4), Validators.pattern('^[0-9]*$')]),
      cardMonth: new FormControl('', [Validators.minLength(1), Validators.pattern('^[0-9]*$')]),
      cardYear: new FormControl('', [Validators.pattern('^[0-9]*$')])
    });


    this.cardSetupForm.controls['cardNumber1'].valueChanges.subscribe((value) => {
      this.cardNum = this.cardSetupForm.controls['cardNumber1'].value;
      this.card = this.getCardType(this.cardNum);
      value || (this.card = null);
      if (value.length === 4) {
        jQuery('input#cardNumber2').focus();
      }
    });
    this.cardSetupForm.controls['cardNumber2'].valueChanges.subscribe((value) => {
      this.cardNum = this.cardSetupForm.controls['cardNumber1'].value + this.cardSetupForm.controls['cardNumber2'].value;
      this.card = this.getCardType(this.cardNum);
      value || (this.card = null);
      if (value.length === 4) {
        jQuery('input#cardNumber3').focus();
      }
    });
    this.cardSetupForm.controls['cardNumber3'].valueChanges.subscribe((value) => {
      this.cardNum = this.cardSetupForm.controls['cardNumber1'].value + this.cardSetupForm.controls['cardNumber2'].value + this.cardSetupForm.controls['cardNumber3'].value;
      this.card = this.getCardType(this.cardNum);
      value || (this.card = null);
      if (value.length === 4) {
        jQuery('input#cardNumber4').focus();
      }
    });
    this.cardSetupForm.controls['cardNumber4'].valueChanges.subscribe((value) => {
      this.cardNum = this.cardSetupForm.controls['cardNumber1'].value + this.cardSetupForm.controls['cardNumber2'].value + this.cardSetupForm.controls['cardNumber3'].value + this.cardSetupForm.controls['cardNumber4'].value;
      this.card = this.getCardType(this.cardNum);
      value || (this.card = null);
    });

    this.couponForm = new FormGroup({
      couponInput: new FormControl('', [Validators.compose([Validators.required, Validators.minLength(2)])])
    });
  }

  applyCoupon() {
    let coupon = null;
    if (this.currentCompany.isOffersEnabled && this.currentPlan.subscription.status === 'in_trial' || this.currentPlan.subscription.status === 'active') {
      coupon = this.couponForm.value.couponInput.toUpperCase();
    }
    jQuery('#estimate-popup').modal('hide');
    this.modalOpened--;
    this.upgradeSubsEstimate(this.upgPlan, coupon);
  }

  cancelCoupon(plan: any) {
    this.appliedCoupon = null;
    this.cancel_coupon = true;
    jQuery('#estimate-popup').modal('hide');
    this.modalOpened--;
    this.upgradeSubsEstimate(plan);
  }

  cardSetup() {
    const cardData = {
      'cardNumber': this.cardNum,
      'cvv': this.cardSetupForm.value.cvv,
      'cardMonth': this.cardSetupForm.value.cardMonth,
      'cardYear': this.cardSetupForm.value.cardYear,
    };
    this.error = {
      status: false,
      message: null
    };
    const self = this;
    jQuery('#btnSetupCard span').text('Please wait...').attr('disabled', true);
    const cardSetup = self.membershipService.resetPayment(cardData).subscribe(
      (success: any) => {
        this.error = {
          status: false,
          message: null
        };
        this.membershipService.setBillingDetail(success);
        jQuery('#cc-modal-new').modal('hide');
        this.modalOpened--;
        jQuery('#btnSetupCard span').text('Save').attr('disabled', false);
        let status = JSON.parse(this.cookieService.readCookie('status'));
        status.cardStatus = success.card.status;
        this.cookieService.createCookie('status', JSON.stringify(status), 1);
        window.toastNotification('Card details updated successfully');
        this.cookieService.eraseCookie('hellobar');
        if (success.hellobar) {
          this.cookieService.createCookie('hellobar', JSON.stringify(success.hellobar), 3);
          if (success.hellobar.message.indexOf('Get 20% off on') !== -1) {
            this.disc20 = true;
          }
        }
        console.log('this.isChangePlanthis.isChangePlanthis.isChangePlanthis.isChangePlan: ', this.isChangePlan);
        if (this.isChangePlan) {
          this.changeSubscription();
        } else {
          setTimeout(function () {
            window.location.reload();
          }, 1000);
        }
        self.initializeForm();
        cardSetup.unsubscribe();
      },
      (error: any) => {
        this.error = {
          status: true,
          message: error.error.err_message
        };
        const start = self.error.message.indexOf(':');
        const end = self.error.message.length;
        if (start !== -1) {
          this.error.message = this.error.message.substring(start + 1, end);
        }
        let email = this.membershipService.getStorageItem('user,emails,0,email');
        //let intercom_link = `https://app.intercom.io/a/apps/${environment.INTERCOM_ID}/respond/inbox/conversation/`;
        let message = `The user ${email} faced some issues in billing. User added card information but we received an error that there some issue in processing payment. Request you to get in touch with the user.`
        this.membershipService.billingPageNotifier({ message, email, status: this.currentPlan.subscription.status })
          .subscribe((data) => {
            console.log("data", data);
          });


        jQuery('#btnSetupCard span').text('Save').attr('disabled', false);
        cardSetup.unsubscribe();
      });
  }

  upgradeSubsAfterCardSetup(obj: any) {
    this.isChangePlan = obj.isChangePlan;
    this.upgradeTo = obj.billing;
  }
  upgradeSubsEstimate(obj: any, coupon: string = null) {
    this.isChangePlan = false;
    this.preloader = true;
    this.upgPlan = obj;
    this.error = {
      status: false,
      message: null
    };
    const data = {
      'plan_id': this.upgPlan.plan.id,
      'prorate': false,
      'end_of_term': true
    };
    const cp = this.currentCompany.billing.chargebee_plan_id.split('_')[0];
    const cc = this.currentCompany.billing.chargebee_plan_id.split('_')[1];
    const up = this.upgPlan.plan.id.split('_')[0];
    const uc = this.upgPlan.plan.id.split('_')[1];

    if (cp === up) {
      if (uc === 'y') {
        data.prorate = true;
        data.end_of_term = false;
      } else {
        data.prorate = false;
        data.end_of_term = true;
      }
    } else {
      if (cp === 'enterprise') {
        data.prorate = false;
        data.end_of_term = true;
      } else if (cp === 'business') {
        if (up === 'enterprise') {
          data.prorate = true;
          data.end_of_term = false;
        } else {
          data.prorate = false;
          data.end_of_term = true;
        }
      } else if (cp === 'essentials') {
        if (up === 'enterprise' || up === 'business') {
          data.prorate = true;
          data.end_of_term = false;
        } else {
          data.prorate = false;
          data.end_of_term = true;
        }
      } else if (cp === 'freelancer') {
        if (up === 'business' || up === 'essentials') {
          data.prorate = true;
          data.end_of_term = false;
        } else {
          data.prorate = false;
          data.end_of_term = true;
        }
      } else {
        data.prorate = true;
        data.end_of_term = false;
      }
    }

    if (this.currentPlan.subscription.status === 'cancelled' || this.currentPlan.subscription.status === 'in_trial') {
      data.prorate = true;
      data.end_of_term = false;
    }
    if (this.currentCompany.is_appsumo_created && this.upgPlan.plan.id.split('_')[0] === 'essentials' && !this.cancel_coupon) {
      coupon = 'APPSUMODEAL' + this.upgPlan.plan.id.split('_')[1].toUpperCase();
    } else if (this.currentCompany.isOffersEnabled && this.currentPlan.subscription.status === 'in_trial' && this.upgPlan.plan.id.split('_')[1] === 'y' && !this.cancel_coupon) {
      coupon = 'UPGRADE20%';
    }
    if (coupon === 'CYBERMONDAY25' && this.currentPlan.subscription.status !== 'cancelled' && this.currentPlan.subscription.status !== 'in_trial' && this.currentPlan.subscription.status !== 'future') {
      this.error.status = true;
      this.error.message = 'Invalid Coupon ' + coupon;
      this.appliedCoupon = null;
      setTimeout(() => {
        this.preloader = false;
        jQuery('#upgrade-plan-popup').modal('show');
      }, 2000);
      return;
    }
    if (coupon === 'UPGRADE20%' && (this.upgPlan.plan.id.split('_')[1] === 'm')) {
      this.error.status = true;
      this.error.message = 'Invalid Coupon ' + coupon;
      this.appliedCoupon = null;
      setTimeout(() => {
        this.preloader = false;
        // jQuery('#estimate-popup').modal('show');
        // jQuery('.modal-backdrop').insertAfter('#estimate-popup');
      }, 2000);
      return;
    }
    if (this.currentPlan.subscription.status !== 'in_trial' && coupon === 'UPGRADE20%') {
      this.error.status = true;
      this.error.message = 'Invalid Coupon ' + coupon;
      this.appliedCoupon = null;
      setTimeout(() => {
        this.preloader = false;
        jQuery('#upgrade-plan-popup').modal('show');
        jQuery('.modal-backdrop').insertAfter('#upgrade-plan-popup');
      }, 2000);
      return;
    }
    if (coupon === 'CYBERMONDAY25' && this.currentCompany.deal_refered === 'BLACK_FRIDAY' && this.currentPlan.subscription.status === 'active') {
      this.error.status = true;
      this.error.message = 'Invalid Coupon ' + coupon;
      this.appliedCoupon = null;
      setTimeout(() => {
        this.preloader = false;
        jQuery('#upgrade-plan-popup').modal('show');
        jQuery('.modal-backdrop').insertAfter('#upgrade-plan-popup');
      }, 2000);
      return;
    }
    if (this.currentCompany.deal_refered === 'BLACK_FRIDAY' && this.currentPlan.subscription.status === 'in_trial' && !this.cancel_coupon) {
      coupon = 'CYBERMONDAY25';
    }
    if (coupon) {
      data['coupon'] = coupon;
      this.appliedCoupon = coupon;
    } else {
      this.appliedCoupon = null;
    }
    this.upgradeTo = {
      billing: data
    };
    const upgradeSubsEstimate = this.membershipService.getUpdateEstimate(data).subscribe(
      (success: any) => {
        success.coupon ? this.couponOffer = success.coupon : this.couponOffer = null;
        this.estimation = new Estimate(success.estimate);
        setTimeout(() => {
          this.preloader = false;
          jQuery('#upgrade-plan-popup').modal('show');
        }, 1000);
        this.error = {
          status: false,
          message: null
        };
        upgradeSubsEstimate.unsubscribe();
      },
      (error: any) => {
        console.error('upgradeSubsEstimate error', error);
        this.error = {
          status: true,
          message: error.error.err_message
        };
        if (this.error.message.includes('coupon')) {
          this.error.message = 'Invalid Coupon ' + this.appliedCoupon;
          this.upgradeTo.billing.coupon = null;
        }
        this.appliedCoupon = null;
        coupon = null;
        setTimeout(() => {
          this.preloader = false;
          jQuery('#upgrade-plan-popup').modal('show');
          jQuery('.modal-backdrop').insertAfter('#upgrade-plan-popup');
        }, 2000);
        upgradeSubsEstimate.unsubscribe();
      }
    );
  }

  removeCoupon() {
    jQuery('#upgrade-plan-popup').modal('hide');
    this.modalOpened--;
    this.cancel_coupon = true;
    this.appliedCoupon = null;
    this.upgradeSubsEstimate(this.upgPlan);
  }

  changeToAnnual() {
    this.error = {
      status: false,
      message: null
    };
    let plan: any = null;
    if (this.upgPlan.plan.id.indexOf('_jv') === -1) {
      plan = this.upgPlan.plan.id.split('_')[0];
    } else if (this.upgPlan.plan.id.indexOf('_jv') !== -1) {
      plan = this.upgPlan.plan.id.split('_')[0] + '_' + this.upgPlan.plan.id.split('_')[2];
    }
    let filterPlan = null;
    this.planItems.filter((pl) => {
      if (pl._id === plan) {
        pl.cbPlan.filter((cbp) => {
          if (plan.indexOf('jv') === -1) {
            if (cbp.id === plan + '_y') {
              filterPlan = cbp;
            }
          } else {
            if (cbp.id === plan.split('_')[0] + '_y_jv') {
              filterPlan = cbp;
            }
          }
        });
      }
    });
    jQuery('#upgrade-plan-popup').modal('hide');
    this.modalOpened--;
    this.upgradeSubsEstimate({ plan: filterPlan });
  }

  UpgradePlan() {
    this.error = {
      status: false,
      message: null
    };
    this.isChangePlan = false;
    let paymentStatus = 'invalid';
    if (this.currentPlan.customer.payment_method.status) {
      paymentStatus = this.currentPlan.customer.payment_method.status;
    }
    const amtdue = this.estimation.invoice_estimate ? this.estimation.invoice_estimate.amount_due : this.estimation.next_invoice_estimate.amount_due;
    if (amtdue && ['expired', 'invalid', 'pending_verification', 'no_card'].includes(paymentStatus)) {
      // (this.cardStatus === null || this.cardStatus === 'expired')) {
      this.isChangePlan = true;
      jQuery('#upgrade-plan-popup').modal('hide');
      this.modalOpened--;
      // jQuery('#cc-modal-new').modal({backdrop: 'static', keyboard: false});
      jQuery('#cc-modal-new').modal('show');
    } else {
      this.changeSubscription();
    }
  }

  changeSubscription() {
    this.preloader = true;
    const self = this;
    const changeSubscription = this.membershipService.updateSubscription(this.upgradeTo).subscribe(
      (success: any) => {
        jQuery('#upgrade-plan-popup').modal('hide');
        this.modalOpened--;
        this.error = {
          status: false,
          message: null
        };
        this.currentCompany = this.subdomainService.currentCompany = new CurrentCompany(success.company);
        this.subdomainService.subDomain.sub_domain = this.currentCompany.sub_domain;
        this.subdomainService.subDomain.company_id = this.currentCompany.id;
        this.subdomainService.subDomain.name = this.currentCompany.name;
        this.cookieService.eraseCookie('hellobar');
        if (success.hellobar) {
          this.cookieService.createCookie('hellobar', JSON.stringify(success.hellobar), 3);
          if (success.hellobar.message.indexOf('Get 20% off on all plans') !== -1) {
            this.disc20 = true;
          }
        }
        const membership = JSON.parse(this.cookieService.readCookie('filepicker_token_json'));
        for (let i = 1; i < membership.length; i++) {
          if (membership[i].key === success.company.sub_domain) {
            membership[i].value = success.subscription.status;
          }
        }
        fbq('track', 'AddPaymentInfo');
        this.cookieService.eraseCookie('filepicker_token_json');
        this.cookieService.createCookie('filepicker_token_json', JSON.stringify(membership), 3);
        ga('markettingteam.send', 'event', 'Payment', 'Submit', 'Payment Successful', 1);
        jQuery('.upgradeBtns').html('change plan').attr('disabled', false);
        jQuery('#changebtn').attr('disabled', false);
        if (this.upgradeTo.billing.end_of_term)
          window.toastNotification('Plan change scheduled successfully');
        else {
          window.toastNotification('Plan Changed Successfully');
          if (this.upgradeTo.billing.plan_id.split('_')[1] === 'm') {
            self.isPlanChangedToMonthly = true;
          }
        }
        setTimeout(function () {
          if (!self.currentCompany.bonus_popup || !self.isPlanChangedToMonthly) {
            window.location.reload();
          } else {
            self.preloader = false;
            jQuery('#modal-bonus-offer').modal('show');
            self.membershipService.bonusPopupViewed().subscribe((result) => {
              self.subdomainService.setCurrentCompany(result);
            }, (error) => {
              console.error('%%%%%%%%%%%%%%%%%%%%%%%%%%%%: ', error);
            });
          }
        }, 1000);
      },
      (error: any) => {
        this.error = {
          status: true,
          message: error.error.err_message
        };
        this.preloader = false;
        jQuery('#upgrade-plan-popup').modal('show');
        jQuery('.modal-backdrop').insertAfter('#upgrade-plan-popup');

        let email = this.membershipService.getStorageItem('user,emails,0,email');
        //let intercom_link = `https://app.intercom.io/a/apps/${environment.INTERCOM_ID}/respond/inbox/conversation/`;
        let message = `The user ${email} faced some issues in billing. User added card information but we received an error that there some issue in processing payment. Request you to get in touch with the user.`
        this.membershipService.billingPageNotifier({ message, email, status: this.currentPlan.subscription.status })
          .subscribe((data) => {
            console.log("data", data);
          });

        jQuery('.btnMakePayment').html('Make Payment').attr('disabled', false);
        if (this.error.message.split(':')[0].trim() === 'coupon') {
          this.error.message = 'Invalid coupon code';
        }
        changeSubscription.unsubscribe();
      }
    );
  }

  closeCCModal() {
    this.isChangePlan = false;
    this.estimation = null;
    this.preloader = false;
  }

  getCardType(number): string {
    if (number.match(new RegExp('^4')) != null) {
      return 'visa';
    } else if (/^(5[1-5][0-9]{14}|2(22[1-9][0-9]{12}|2[3-9][0-9]{13}|[3-6][0-9]{14}|7[0-1][0-9]{13}|720[0-9]{12}))$/.test(number)) {
      return 'mastercard';
    } else if (number.match(new RegExp('^3[47]')) != null) {
      return 'amex';
    } else if (number.match(new RegExp('^(6011|622(12[6-9]|1[3-9][0-9]|[2-8][0-9]{2}|9[0-1][0-9]|92[0-5]|64[4-9])|65)')) != null) {
      return 'discover';
    } else if (number.match(new RegExp('^36')) != null) {
      return 'diners';
    } else if (number.match(new RegExp('^35(2[89]|[3-8][0-9])')) != null) {
      return 'jcb';
    } else {
      return null;
    }
  }
}
