import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { WebhookService } from '../../../shared/services/webhook.service';
import { Script } from '../../../shared/services';
import {FeatureAuthService} from '../../../shared/services/feature-access.service';
import {SubDomainService} from '../../../shared/services/subdomain.service';
import { environment } from './../../../../environments/environment';
declare var jQuery: any;
declare var window: any;
declare var moment: any;
@Component({
  selector: 'og-webhook',
  templateUrl: './webhook.component.html',
  styleUrls: ['./webhook.component.css']
})

export class WebhookComponent implements OnInit, AfterViewInit, OnDestroy {
  gwebhookForm: FormGroup;
  isBasicAuth: boolean;
  error: any;
  success: any;
  company: string;
  globalWebhooks: any;
  loading: boolean;
  addWebhook: boolean;
  webhookRequests: any;
  startDate: any;
  endDate: any;
  loadingLogs: boolean;
  webhookLogsOffset: number;
  webhookLogsCount: number;
  pageNumber: number;
  date: any;
  JSON: any;
  gwId: String;
  viewI: String;
  webhooklogDetailRequest: any;
  webhooklogDetailResponse: any;
  webhooklogDetailResponseBody: any;
  isSkip: boolean;
  constructor(private formBuilder: FormBuilder,
    private webhookService: WebhookService,
    private script: Script,
    public _featureAuthService: FeatureAuthService,
    private subdomainService: SubDomainService,) {
    this.isBasicAuth = false;
    this.error = {
      status: false,
      code: 500,
      message: ''
    };
    this.success = {
      status: false,
      code: 200,
      message: ''
    };
    this.company = '';
    this.globalWebhooks = null;
    this.loading = true;
    this.addWebhook = false;
    this.webhookRequests = [];
    this.startDate = moment(new Date()).format('MM/DD/YYYY');
    this.endDate = moment(new Date()).format('MM/DD/YYYY');
    this.loadingLogs = false;
    this.webhookLogsOffset = 0;
    this.webhookLogsCount = 0;
    this.date = moment;
    this.JSON = JSON;
    this.gwId = null;
    this.pageNumber = 1;
    this.webhooklogDetailRequest = null;
    this.webhooklogDetailResponse = null;
    this.webhooklogDetailResponseBody = null;
    this.viewI = null;
    this.isSkip = false;
  }
  ngOnInit() {
    jQuery.material.init();
    this.initForm();
    this.company = window.location.href.split('.')[0].split('//')[1];
    this.getCompanyWebhook();
  }
  initForm() {

    if (this.globalWebhooks && this.globalWebhooks.basicAuth) {
      this.isBasicAuth = this.globalWebhooks.basicAuth;
      this.gwebhookForm = this.formBuilder.group({
        webhookName: [this.globalWebhooks.webhook_name, Validators.compose([Validators.required])],
        webhookUrl: [this.globalWebhooks.webhook_url, Validators.compose([Validators.required])],
        events: [this.globalWebhooks.events, Validators.compose([Validators.required])],
        webhookUname: [this.globalWebhooks.basicAuthUname, Validators.compose([Validators.required])],
        webhookPassword: [this.globalWebhooks.basicAuthPassword, Validators.compose([Validators.required])]
      });
    } else if (this.globalWebhooks && !this.globalWebhooks.basicAuth) {
      this.isBasicAuth = this.globalWebhooks.basicAuth;
      this.gwebhookForm = this.formBuilder.group({
        webhookName: [this.globalWebhooks.webhook_name, Validators.compose([Validators.required])],
        webhookUrl: [this.globalWebhooks.webhook_url, Validators.compose([Validators.required])],
        events: [this.globalWebhooks.events, Validators.compose([Validators.required])]
      });
    } else {
      this.gwebhookForm = this.formBuilder.group({
        webhookName: ['', Validators.compose([Validators.required])],
        webhookUrl: ['', Validators.compose([Validators.required])],
        events: [['LEADS', 'VISITS'], Validators.compose([Validators.required])]
      });
    }
  }
  ngAfterViewInit() {
    const self = this;
    const interval = setInterval(function() {
      if (window.Intercom){
        if (!self._featureAuthService.features.integrations.webhook) {
          window.location.href = environment.PROTOCOL + self.subdomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/settings/';
        } else {
          window.Intercom('update', { 'app_current_page': 'gwebhook' });
          window.Intercom('update', { 'app_current_page_url': window.location.href });
        }
        clearInterval(interval);
      }
    }, 1000);
    setTimeout(() => {
      this.script.load('daterangepicker').then((data) => {
        this.initDatePicker();
        jQuery('body').addClass('setting-bg');
      }).catch((error) => { });
    }, 3000);
  }

  ngOnDestroy() {
    jQuery('body').removeClass('setting-bg');
  }
  showHideBasicAuth() {
    this.isBasicAuth = !this.isBasicAuth;
    if (this.isBasicAuth) {
      this.gwebhookForm = this.formBuilder.group({
        webhookName: [this.gwebhookForm.value.webhookName, Validators.compose([Validators.required])],
        webhookUrl: [this.gwebhookForm.value.webhookUrl, Validators.compose([Validators.required])],
        events: [this.gwebhookForm.value.events, Validators.compose([Validators.required])],
        webhookUname: [this.globalWebhooks ? this.globalWebhooks.basicAuthUname : '', Validators.compose([Validators.required])],
        webhookPassword: [this.globalWebhooks ? this.globalWebhooks.basicAuthPassword : '', Validators.compose([Validators.required])]
      });
    } else {
      this.gwebhookForm = this.formBuilder.group({
        webhookName: [this.gwebhookForm.value.webhookName, Validators.compose([Validators.required])],
        webhookUrl: [this.gwebhookForm.value.webhookUrl, Validators.compose([Validators.required])],
        events: [this.gwebhookForm.value.events, Validators.compose([Validators.required])]
      });
    }
  }
  testNewWebhook() {
    if (this.gwebhookForm.valid) {
      jQuery('#btnTestSaveWebhook').html('Please wait...').attr('disabled', true);
      const data = this.gwebhookForm.value;
      if (!this.isBasicAuth) {
        data.webhookUname = '';
        data.webhookPassword = '';
      }
      data['company'] = this.company;
      this.webhookService.testWebhook(data).subscribe((success: any) => {
        if (success.response.statusCode >= 400 && success.response.statusCode <= 511) {
          this.error['status'] = true;
          this.error['code'] = success.response.statusCode;
          this.error['message'] = '';
          this.hideMessages();
        } else {
          this.saveGlobalWebhook(data, success);
          this.webhookLogsOffset = 0;
          this.pageNumber = 1;
          this.hideMessages();
        }
        jQuery('#btnTestSaveWebhook').html('Test &amp; Save').attr('disabled', false);
      }, (error: any) => {
        jQuery('#btnTestSaveWebhook').html('Test &amp; Save').attr('disabled', false);
        console.error('testNewWebhook Error: ', error);
        this.error.status = true;
        this.error.code = 500;
        this.error.message = 'Server Error';
        this.hideMessages();
      });
    }
  }
  saveGlobalWebhook(webhook, testData) {
    const data = {
      webhook: webhook,
      testData: testData
    };
    jQuery('#btnTestSaveWebhook').html('Please wait...').attr('disabled', true);
    this.webhookService.saveGlobalWebhook(data).subscribe((success: any) => {
      this.globalWebhooks = null;
      if (!jQuery.isEmptyObject(success)) {
        this.globalWebhooks = success;
        setTimeout(() => {
          this.initDatePicker();
        }, 2000);
      }
      this.success = {
        status: true,
        code: testData.statusCode,
        message: ''
      };
      jQuery('#btnTestSaveWebhook').html('Test &amp; Save').attr('disabled', false);
      this.addWebhook = false;
      this.initForm();
    }, (error) => {
      console.error('saveGlobalWebhook error: ', error);
      jQuery('#btnTestSaveWebhook').html('Test &amp; Save').attr('disabled', false);
    });
  }
  hideMessages() {
    setTimeout(() => {
      this.error = {
        status: false,
        code: 500,
        message: ''
      };
      this.success = {
        status: false,
        code: 200,
        message: ''
      };
    }, 3000);
  }
  getCompanyWebhook() {
    this.webhookService.getCompanyWebhook(this.company).subscribe((success: any) => {
      this.globalWebhooks = null;
      if (!jQuery.isEmptyObject(success)) {
        this.globalWebhooks = success;
      }
      this.initForm();
      this.loading = false;
      this.viewWebhookRequest();
    }, (error: any) => {
    });
  }
  containsObject(obj, list) {
    for (let i = 0; i < list.length; i++) {
      if (list[i]._id === obj._id) {
        return i;
      }
    }
    return -1;
  }

  initDatePicker() {
    const self = this;
    jQuery('#webhookLogDate').daterangepicker({
      buttonClasses: ['btn', 'btn-sm'],
      applyClass: 'btn-danger',
      cancelClass: 'btn-inverse',
      startDate: moment(self.startDate).format('MM/DD/YYYY'),
      endDate: moment(self.endDate).format('MM/DD/YYYY')
    });
    self.viewWebhookRequest();
    jQuery('#webhookLogDate').on('apply.daterangepicker', function (ev, picker) {
      const startEndDate = ev.currentTarget.value.split('-');
      self.startDate = moment(new Date(startEndDate[0].trim())).format('MM/DD/YYYY');
      self.endDate = moment(new Date(startEndDate[1].trim())).format('MM/DD/YYYY');
      self.webhookLogsOffset = 0;
      self.pageNumber = 1;
      self.viewWebhookRequest();
    });
  }

  viewWebhookRequest() {
    // this.webhookRequests = [];
    // this.webhookLogsCount = 0;
    if (this.globalWebhooks) {
      this.loadingLogs = true;
      const webhookLog = this.webhookService.getGlobalWebhookLeadlog(this.globalWebhooks._id, this.webhookLogsOffset, this.startDate, this.endDate).subscribe(
        (webhooklogs: any) => {
          if (webhooklogs.logs.length) {
            this.webhookRequests = webhooklogs.logs;
            this.webhookLogsCount = webhooklogs.count;
          } else {
            this.webhookRequests = [];
            this.webhookLogsCount = 0;
          }
          this.loadingLogs = false;
          webhookLog.unsubscribe();
          this.success = null;
          this.isSkip = false;
        },
        (error: any) => {
          console.error('viewWebhookRequest error', error);
          webhookLog.unsubscribe();
        }
      );
    }
  }

  oldWLog() {
    this.isSkip = true;
    jQuery('#oldLog').attr('disabled', true).text('Please wait...');
    jQuery('#newLog').attr('disabled', true);
    this.webhookLogsOffset -= 5;
    this.viewWebhookRequest();
    this.loadingLogs = true;
    this.pageNumber--;
  }

  newWLog() {
    this.isSkip = true;
    jQuery('#oldLog').attr('disabled', true);
    jQuery('#newLog').attr('disabled', true).text('Please wait...');
    this.webhookLogsOffset += 5;
    this.viewWebhookRequest();
    this.loadingLogs = true;
    this.pageNumber++;
  }

  showWebhookDetail(webhooklog) {
    this.webhooklogDetailRequest = JSON.parse(webhooklog.request);
    this.webhooklogDetailResponse = JSON.parse(webhooklog.response);
    this.webhooklogDetailResponseBody = this.webhooklogDetailResponse.body;
    jQuery('#view-webhook-req').modal('show');
  }
  WebhookFor(event) {
    if (event === 'LEADS') {
      const index = this.gwebhookForm.value.events.indexOf('LEADS');
      if (index > -1) {
        this.gwebhookForm.value.events.splice(index, 1);
      } else {
        this.gwebhookForm.value.events.push('LEADS');
      }
    }
    if (event === 'VISITS') {
      const index = this.gwebhookForm.value.events.indexOf('VISITS');
      if (index > -1) {
        this.gwebhookForm.value.events.splice(index, 1);
      } else {
        this.gwebhookForm.value.events.push('VISITS');
      }
    }
    if (this.gwebhookForm.value.events.length === 0) {
      if (event === 'LEADS') {
        this.gwebhookForm.value.events.push('VISITS');
      }
      if (event === 'VISITS') {
        this.gwebhookForm.value.events.push('LEADS');
      }
    }
  }

  activeWebhook() {
    const self = this;
    if (!jQuery.isEmptyObject(this.globalWebhooks)) {
      jQuery('#status').attr('disabled', true);
      const activateWebhook = self.webhookService.activateGlobalWebhook(this.globalWebhooks)
        .subscribe(
          (success: any) => {
            jQuery('#status').attr('disabled', false);
            self.globalWebhooks = success;
            activateWebhook.unsubscribe();
            window.toastNotification('Global webhook is ' + self.globalWebhooks.status);
          },
          (error: any) => {
            // self.error = error.error;
            console.log('error: ', error);
          }
        );
    }
  }
}
