  initJQuery() {
    let $target;
    const self = this;
    const planExpand = setInterval(() => {
      if (!this.loading.plan) {
        jQuery('.expand-sub').click(function (event) {
          $target = jQuery(event.target);
          jQuery(this).toggleClass('open');
          if(jQuery(this).hasClass('open')) {
            $target.closest('.expand-sub').find('.exp_icon').html('remove_circle');
          } else {
            $target.closest('.expand-sub').find('.exp_icon').html('add_circle');
          }
          $target.closest('.expand-sub').find('.detail').slideToggle();
        });
        jQuery('.expand-sub-rs').click(function (event) {
          $target = jQuery(event.target);
          jQuery(this).toggleClass('open');
          if(jQuery(this).hasClass('open')) {
            $target.closest('.expand-sub-rs').find('.exp_icon').html('remove_circle');
          } else {
            $target.closest('.expand-sub-rs').find('.exp_icon').html('add_circle');
          }
          jQuery(this).children('.detail').slideToggle();
        });
        jQuery('.expand-sub-all').click(function (event) {
          if (jQuery(this).hasClass('open')) {
            jQuery('.expand-sub').removeClass('open');
            jQuery('.detail').slideUp('slow');
          } else {
            jQuery('.expand-sub').addClass('open');
            jQuery('.detail').slideToggle();
          }
          jQuery(this).toggleClass('open');
        });

        clearInterval(planExpand);
      }
    }, 1000);
    const progress = setInterval(() => {
      if (!this.loading.projects) {
        this.progress.leadsProgress = (this.currentCompany.current_limit.leads !== -1) ? (this.currentCompany.current_usage.leads / this.currentCompany.current_limit.leads) * 100 : 0;
        this.progress.trafficProgress = (this.currentCompany.current_limit.traffic !== -1) ? (this.currentCompany.current_usage.traffic / this.currentCompany.current_limit.traffic) * 100 : 0;
        this.progress.calcProgress = (this.currentCompany.current_limit.calculators !== -1) ? ((this.CompanyProjects.length ? this.CompanyProjects.length : 0) / this.currentCompany.current_limit.calculators) * 100 : 0;
        jQuery('.leadsProgress').css('width', this.progress.leadsProgress + '%');
        jQuery('.trafficProgress').css('width', this.progress.trafficProgress + '%');
        jQuery('.calcProgress').css('width', this.progress.calcProgress + '%');
        this.expandForSmallScreen();
        clearInterval(progress);
      }
    }, 300);
  }

  expandForSmallScreen() {
    jQuery('.toggle-sub-more').click(function (event) {
      const $target = jQuery(event.target);
      const d = jQuery(this).children('i').html();
      if (d === 'add') {
        jQuery(this).children('i').html('remove');
        jQuery(this).children('p').html('show less');
      } else {
        jQuery(this).children('i').html('add');
        jQuery(this).children('p').html('show more');
      }
      jQuery(this).parents('.pricing-bottom').toggleClass('open');
      $target.closest('.pricing-bottom').find('.features').slideToggle().removeClass('rs-hide');
      this.initJQuery();
    });
  }

