  sendMessageToIntercom(message = '') {
    this.openIntercom(message);
    // setTimeout(() =>
    this._intercomService.triggerClickToIntercom();
    // , 100);
  }
  sendFeatureRequirement(text) {
    if (text && text.trim()) {
      this.triggerEmailAndIntercom(this.textFeatureSlide(text));
    } else {
      // this.openIntercom();Need features from higher plans
      this.triggerEmailAndIntercom('Need features from higher plans');
    }
    this.resetValues();
  }
  slideToAdvice(otherOption) {
    this.otherOption.markAsDirty();
    if (this.otherOption.valid && otherOption && otherOption.trim()) {

      //this.markAll('hide', ['step2-advice']);
      this.markAll('hide', ['step2-flow2-chat']);

      this.errorMessageReasons = false;
      this.sendSelectedOptions();
      // this.errorMessageAdvice = false;
      this.triggerEmail();
    } else {
      this.errorMessageReasons = true;
      return;
    }
  }
  slideToChat(adviceText) {
    this.adviceText.markAsDirty();
    if (this.adviceText.valid && adviceText && adviceText.trim()) {
      this.markAll('hide', ['step2-flow2-chat']);
      // this.sendSelectedOptions();
      this.sendSelectedOptions();
      this.errorMessageAdvice = false;
      this.triggerEmail();
    } else {
      this.errorMessageAdvice = true;
      return;
    }
  }
  resetValues() {
    this.featureText = '';
    this.otherOption.reset();
    this.adviceText.reset();
    this.reasons = [];
    this.resetCheckBoxes();
    this.proceedSelection = false;
    this.errorMessageReasons = false;
    this.errorMessageAdvice = false;

  }
  sendSelectedOptions() {
    let text = '';
    if (this.proceedSelection && !this.featureText.trim()) {
      text += 'Need features from higher plans';
    }
    text += this.textToSend();
    if (text) {
      this.sendMessageToIntercom(text);
    }
  }
  textToSend(delimeter = '\n') {
    let text = '';
    text += this.textFeatureSlide(this.featureText, delimeter);
    text += this.textReasonsSlide((this.reasons.reduce((acc, ele) => {
      acc += '- ' + ele + delimeter;
      return acc;
    }, '') + (this.otherOption.value ? this.otherOption.value : '')), delimeter);
    text += this.textAdviceSlide(this.adviceText.value, delimeter);
    return text;
  }
  textFeatureSlide(text, delimeter = '\n') {
    if (!text) return '';
    return `Features I need: ${text}${delimeter}`;
  }
  textReasonsSlide(text, delimeter = '\n') {
    if (!text) return '';
    return `Considering cancelation because:${delimeter} ${text} ${delimeter}`;
  }
  textAdviceSlide(text, delimeter = '\n') {
    if (!text) return '';
    return `Advice for Outgrow: ${text}${delimeter}`;
  }
  resetCheckBoxes() {
    this.checkBoxes.forEach((chk) => {
      chk.nativeElement.checked = false;
    });
  }
  async triggerEmailAndIntercom(text) {
    try {
      this.sendMessageToIntercom(text);
      await this.membershipService.cancelMembership(text).toPromise();
      this.resetValues();
    } catch (e) {
      console.log(e);
    }
  }
  async triggerEmail() {
    let text = '';
    if (this.proceedSelection && !this.featureText.trim()) {
      text += 'Need features from higher plans';
    }
    text += this.textToSend('<br/>');
    try {
      if (text) {
        await this.membershipService.cancelMembership(text).toPromise();
        this.resetValues();
      }

    } catch (e) {
      console.log(e);
    }
  }
  validityCheck(text, p) {
    if (text.length < 85) {
      p.classList.remove('hide');
    } else {
      p.classList.add('hide');

    }
  }
  sendRequirements(featureText) {
    this.markAll('hide', ['step2-customFeatures']);
    this.sendFeatureRequirement(featureText);
  }
  sendIntercomMsg(type) {
    let message = '';
    if (type === 'smlBusiness') {
      message = 'I am on your pricing page and am interested to see if I qualify for your small business incentive program as I would like a special deal. Do I satisfy your small business program criteria ?';
    } else if (type === 'EnterpriseAgency') {
      message = 'Interested in Enterprise / Agency plans';
    }
    this.sendMessageToIntercom(message);
    // window.Intercom('showNewMessage', message);
    // setTimeout(() => this.triggerClickToIntercom(), 100);
  }
