import {Component, OnInit, OnDestroy} from '@angular/core';
import {MembershipService} from '../../../../shared/services/membership.service';

declare let jQuery: any;

@Component({
  selector: 'og-invoice-new',
  templateUrl: './invoice-new.component.html',
  styleUrls: ['./invoice-new.component.css']
})
export class InvoiceNewComponent implements OnInit, OnDestroy {
  loading: boolean;
  invoiceItems;
  subscribes: any[];

  constructor(private membershipService: MembershipService) {
    this.loading = true;
    this.invoiceItems = [];
    this.subscribes = [];
  }

  ngOnInit() {
    this.initInvoiceItems();
  }

  ngOnDestroy() {
    this.subscribes.forEach((subscribe) => {
      subscribe.unsubscribe();
    });
  }

  initInvoiceItems() {
    this.subscribes.push(this.getInvoices());
    const self = this;
    this.subscribes.push(this.membershipService.getInvoiceItems().subscribe((invoiceItems) => {
      self.invoiceItems = invoiceItems;
        this.loading = false;
    }));
  }

  getInvoices() {
    return this.membershipService.getInvoices().subscribe((success: any) => {
      this.membershipService.setInvoiceItems(success.list);
    }, (error: any) => {
      console.error('getInvoices error', error);
    });
  }

  getInvoicePdf(invoiceId: any) {
    console.log('invoiceIdinvoiceId', invoiceId);
    jQuery('#' + invoiceId).text('Please wait...');
    const getInvoicePdf = this.membershipService.getInvoicesPdf(invoiceId)
      .subscribe(
        (success: any) => {
          window.location.assign(success.download.download_url);
          getInvoicePdf.unsubscribe();
          jQuery('#' + invoiceId).html('<i class="fa fa-download" aria-hidden="true"></i> PDF');
        },
        (error: any) => {
          getInvoicePdf.unsubscribe();
          jQuery('#' + invoiceId).html('<i class="fa fa-download" aria-hidden="true"></i> PDF');
        }
      );
  }
}
