import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { CookieService, UserService } from '../../../../shared/services';
declare var jQuery: any;
declare var moment :any;
declare var window: any;
@Component({
  selector: 'two-fact-auth',
  templateUrl: './two-fact-auth.component.html',
  styleUrls: ['./two-fact-auth.component.css']
})
export class TwoFactAuthComponent implements OnInit {
  public twoFactorInfo: any = {};
  public recovery_codes: any;
  public show_recovery_codes: boolean;
  public downloadUrl: any;
  public created_at:any;
  public force_setup: any;
  cookie: string;
  storage: any;
  role: string;
  enabledUsers: any;
  totalUsers: any;
  constructor(private userService: UserService,
    public sanitizer: DomSanitizer,
    public cookieService:CookieService) {
      this.enabledUsers = 0;
      this.totalUsers = 0;
     }

  ngOnInit() {
    this.role = this.cookieService.readCookie('role');
    this.cookie = this.cookieService.readCookie('storage');
    this.storage = this.cookie != null ? JSON.parse(this.cookie) : '';
    this.force_setup = this.storage.mfa ? this.storage.mfa.force_setup : '';
    this.getTwoFactorDetails();
  }
  getTwoFactorDetails() {
    this.userService.getTwoFactorDetails().subscribe((res)=>{
      this.twoFactorInfo = res.two_fact_auth;
      this.force_setup = res.force_setup;
      this.enabledUsers = res.enabled_count;
      this.totalUsers = res.total_count;
      this.twoFactorInfo.is_configured ? this.onRecoveryChange(true) : this.onRecoveryChange(false)
      this.created_at = this.twoFactorInfo.created_at ? moment(this.twoFactorInfo.created_at).format('DD-MM-YYYY'):'';
    },(err)=>{

    });
  }
  async openModal(event) { 
    if (event.target.checked) {
      const res = await this.userService.getSecret();
      this.twoFactorInfo.secret = res || {};
      setTimeout(() => {
        jQuery('#tfa-m-cap').modal({
          backdrop: 'static',
          keyboard: false
        });
      }, 100)
    } else {
      this.userService.disableTwofactor().subscribe((res) => {
        this.twoFactorInfo = res.two_fact_auth;
        this.force_setup = res.force_setup;
      },
        (err) => {

        })
    }
  }
  handleEvent(event) {
    this.twoFactorInfo = event.two_fact_auth;
    this.force_setup = event.force_setup;
    this.created_at = this.twoFactorInfo.created_at ? moment(this.twoFactorInfo.created_at).format('DD-MM-YYYY'):'';
    this.twoFactorInfo.is_configured ? this.onRecoveryChange(true) : this.onRecoveryChange(false)
  }
  generateNewCodes() {
    this.userService.generateRecoveryCodes().subscribe((res) => {
      this.twoFactorInfo.recovery_codes = res.codes;
      this.generateBlobUrl()
    },
      (err) => {

      })
  }
  onRecoveryChange(event) {
    if (event) {
      this.show_recovery_codes = true;
      jQuery('#recoveryCode').removeClass('auth-card-outer');
      jQuery('#recoveryCode').addClass('auth-card-outer-rec');
    } else {
      this.show_recovery_codes = false;
      jQuery('#recoveryCode').addClass('auth-card-outer');
      jQuery('#recoveryCode').removeClass('auth-card-outer-rec');
    }
    this.generateBlobUrl()
  }
  generateBlobUrl() {
    let codesText = 'Your recovery Codes \r\n';
    codesText = codesText + this.twoFactorInfo.recovery_codes.map(recovery_code => recovery_code.code + '\r\n').join('');
    const codesToDownload = new Blob([codesText], { type: "text/troff" });
    this.downloadUrl = window.URL.createObjectURL(codesToDownload);
    this.downloadUrl = this.sanitizer.bypassSecurityTrustUrl(this.downloadUrl);
  }
  isMfaMandatoryForAll(event) {
    if(event.target.checked) {
      this.userService.enableMfaForAll().subscribe((res) => {
        this.twoFactorInfo = res.two_fact_auth;
        this.force_setup = res.force_setup;
        this.enabledUsers = res.enabled_count;
        this.totalUsers = res.total_count;
      },
        (err) => {
  
        })
    } else {
      this.userService.disableMfaForAll().subscribe((res) => {
        this.twoFactorInfo = res.two_fact_auth;
        this.force_setup = res.force_setup;
      },
        (err) => {
  
        })
    }
  }
  responsiveAlert1(){
    if (this.role === 'MANAGER' && (this.twoFactorInfo.activation || this.force_setup)) {
      window.toastNotification('2FA is enabled for (one of) your organization(s)');
    } else if (this.role === 'ADMIN' && (this.twoFactorInfo.activation || this.force_setup)) {
      window.toastNotification("The '2FA for all Users' toggle for (one of) your organization(s) is turned ON. Turn that OFF to disable 2FA");
    }
  }
  responsiveAlert2(){
    if (!this.twoFactorInfo.is_configured) {
      window.toastNotification('Please setup your 2FA authentication before enabling this feature');
    }
  }
}