import { CustomFeatures } from './../../../../shared/models/company';
import { CompanyService } from './../../../../shared/services/company.service';
import { environment } from './../../../../../environments/environment';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Component, OnInit, AfterViewInit } from '@angular/core';
import { User } from '../../../../shared/models/user';
import { Customer } from '../../../../shared/models/currentPlan';
import { EmailValidator } from './../../../../shared/validators/email.validator';
import {UserService} from '../../../../shared/services/user.service';
import {CookieService} from '../../../../shared/services/cookie.service';
import {MembershipService} from '../../../../shared/services/membership.service';
import {SubDomainService} from '../../../../shared/services/subdomain.service';
import {Script} from '../../../../shared/services/script.service';
declare var jQuery:any;
declare var moment:any;
declare var google:any;
declare var ga:any;
// declare var _kmq:any;
declare var window: any;
declare var filestack: any;

@Component({
  selector: 'og-basic-details',
  templateUrl: './basicdetails.component.html',
  styleUrls: ['./basicdetails.component.css', './../../settings.component.css']
})

export class BasicDetailsComponent implements OnInit,AfterViewInit {
  basicDetailsForm: FormGroup;
  emailForm: FormGroup;
  timezones: any;
  model: any = new User({});
  BillingDetails: Customer = new Customer(null);
  submitted = false;
  active = true;
  error = false;
  protocol: string = "";
  success = false;
  Message: any = '';
  user_email: String;
  autocompletesp: any;
  autocomplete: any;
  locationId: any;
  address: any;
  isPlaceExist: Boolean = false;
  hostnameRegexp: RegExp = new RegExp('^https?://.+?/');
  cookie: any;
  storage: any;
  hostUrl: string = '';
  currentCompany: any;
  customFeatures: any;
  filePickerKey: any = environment.FILE_PICKER_API;
  whPlan = ['outgrow-whitelabel-10c_d',
    'outgrow-whitelabel-30c_d',
    'outgrow-whitelabel-100c_d'];
  // @HostBinding('class.someClass') someField: bool = false;
  constructor(
    public _userService: UserService,
    public fb:FormBuilder,
    public _cookieService : CookieService,
    public _script :Script,
    public _membershipService:MembershipService,
    public _subDomainService:SubDomainService,
    public _companyService:CompanyService,
  ) {
    this.currentCompany = _subDomainService.currentCompany;
  }

  ngOnInit() {
    jQuery.material.init();
    this.hostUrl = window.location.href.split('/')[2];
    this.protocol = window.location.href.split('/')[0];
    this.cookie = this._cookieService.readCookie('storage');
    this.storage = this.cookie != null ? JSON.parse(this.cookie) : '';
    this.basicDetailsForm = this.fb.group({
      name: [this.model.name, Validators.compose([
        Validators.pattern('^[a-zA-Z ]*$'), Validators.required, Validators.minLength(4)
      ])],
      phone: [this.model.phone, Validators.compose([
        Validators.pattern('[-+0 -9]+$'), Validators.minLength(5)
      ])],
      location: [this.model.location],
      timezone: [this.model.timezone]
    });
    this.emailForm = this.fb.group({
      email: [this.model.emails, Validators.compose([
        Validators.required, Validators.minLength(4), EmailValidator.format
      ])],
      password: [this.model.password, Validators.compose([
        Validators.required
      ])]
    });

  }

  ngAfterViewInit() {
    this._script.load('googleLocation', 'timeZoneMin')
      .then((data: any) => {
        this.initialize();
        this.getBasicDetails();
        this.customFeatures = this._subDomainService.company_custom_features;

      })
      .catch((error) => {
        //console.log('Script not loaded', error);
      });
  }

  initialize() {
    localStorage.removeItem('pid');
    let locationElement: any = document.getElementById('location');
    this.autocomplete = new google.maps.places.Autocomplete(
      (locationElement),
      { types: ['(cities)'] }
    );
    var self = this;
    google.maps.event.addListener(self.autocomplete, 'place_changed', function () {
      let place = this.getPlace();
      if (place.place_id) {
        self.isPlaceExist = true;
        self.model.location = place.formatted_address;
        locationElement.value = place.formatted_address;
        localStorage.setItem('pid', self.model.location);
        jQuery('#saveBasicDetails').attr('disabled', false);
      }
    });
  }

  chkLocation() {
    let pid = localStorage.getItem('pid');
    let locationElement: any = document.getElementById('location');
    let self = this;
    if (self.isPlaceExist === false && pid === null) {
      locationElement.value = '';
      localStorage.removeItem('pid');
      jQuery('#saveBasicDetails').attr('disabled', true);
    } else {
      jQuery('#saveBasicDetails').attr('disabled', false);
      locationElement.value = pid;
      self.model.location = locationElement.value;
    }
  }

  getBasicDetails() {
    // let timeZones = moment.tz.names();
    this.timezones = moment.tz.names();
    for (let timezone in this.timezones) {
      // this.timezones[timezone] = timeZones[timezone] +' '+ moment.tz(timeZones[timezone]).format('Z z');
      this.timezones[timezone] = this.timezones[timezone] + ' ' + moment.tz(this.timezones[timezone]).format('Z z');
    }
    let self: any = this;
    self._userService.getBasicDetails()
      .subscribe(
        (data: any) => {
          self.model = data;
          if (self.model.timezone.length === 0)
            self.model.timezone = 'Select a Timezone';
          self.model.emails = self.model.emails[0].email;
          this.user_email = self.model.emails;
          if (self.model.location.length === 0) {
            // jQuery.getJSON('//freegeoip.net/json/?callback=?', function(data:any) {
            //   ip = data.ip;
            //   self.model.location = data.city+', '+data.region_name+' '+data.zip_code+', '+data.country_name;
            // });
            // jQuery.getJSON('//geoplugin.net/json.gp?jsoncallback=?ip='+ip,function(data:any){
            //   console.log('location data',data);
            // });
            let pos: any;
            navigator.geolocation.getCurrentPosition(function (position) {
              pos = {
                lat: position.coords.latitude,
                lng: position.coords.longitude
              };
              let latlng = new google.maps.LatLng(pos.lat, pos.lng);
              let geocoder = new google.maps.Geocoder();
              geocoder.geocode({ 'latLng': latlng }, function (results: any, status: any) {
                if (status === google.maps.GeocoderStatus.OK) {
                  if (results[1]) {
                    self.model.location = results[1].formatted_address;
                  }
                }
              });
            });
          }
        },
        (response: any) => {
          self.Message = response.error.err_message;
          jQuery('#basicMessageModal').modal('show');
        }
      );
  }

  onSubmit() {
    jQuery('#saveBasicDetails').html('Please Wait...');
    this._userService.updateBasicDetails(this.model)
      .subscribe(
        (data: any) => {
          this.getBasicDetails();
          this.updateBiiling();
          jQuery('#saveBasicDetails').html('Update');
          this.storage.user.name = data.name;
          this.cookie = JSON.stringify(this.storage);
          this._cookieService.createCookie('storage', this.cookie, 3);
          jQuery('.name-title').html('&nbsp;' + data.name);
          window.toastNotification('Basic Details Updated!');
          /*---- Tracking code goes here ----*/
          ga('markettingteam.send', 'event', 'Settings', 'Submit', 'EditInfo');
          // _kmq.push(['record', 'Settings Updated account basic info']);
          /*---------------------------------*/
        },
        (response: any) => {
          this.Message = response.error.err_message;
          jQuery('#saveBasicDetails').html('Update');
        }
      );
  }

  updateBiiling() {
    let self = this;
    let getPaymentDeatils = self._membershipService.updateCustomer(self.model.name)
      .subscribe(
        (success: any) => {
          this.BillingDetails = new Customer(success.customer);
        },
        (error: any) => {
          //console.log('error',error);
          getPaymentDeatils.unsubscribe();
        }
      );
  }

  setFalse() {
    this.error = false;
    this.success = false;
  }

  updateEmail() {
    jQuery('#updateEmail').html('Please Wait...');
    jQuery('#updateEmail').attr('disabled', true);
    if (this.storage.user.emails[0].email !== this.model.emails) {
      this._userService.updateEmail(this.storage.user.emails[0].email, this.model.emails, this.model.password)
        .subscribe(
          (data: any) => {
            //console.log('UPDATE EMAIL one', storage.user.emails);
            //console.log('UPDATE EMAIL two', data.emails);
            this.storage.user.emails = data.emails;
            this.cookie = JSON.stringify(this.storage);
            this._cookieService.createCookie('storage', this.cookie, 3);
            this.getBasicDetails();
            jQuery('#updateEmail').html('Change Email');
            jQuery('#updateEmail').attr('disabled', false);
            jQuery('#change-email').modal('hide');
            this.Message = 'Email Updated!';
            this.success = true;
            window.toastNotification("Email Updated");
          },
          (response: any) => {
            let error_code = response.error.code;
            if (error_code === 'E_UNIQUE_USERNAME_VALIDATION' ||
              error_code === 'E_UNIQUE_EMAIL_VALIDATION' ||
              error_code === 'E_UNIQUE_UNIDENTIFIED_VALIDATION'
            ) {
              this.Message = ' Email is already registered with us!';
            }
            else if (error_code === 'E_USER_NOT_FOUND') {
              this.Message = response.error.message;
            }
            else {
              this.Message = (response.error.err_errors['emails.0.email']) ?
                response.error.err_errors['emails.0.email'].message :
                response.error.err_message;
            }
            this.error = true;
            jQuery('#updateEmail').html('Change Email');
            jQuery('#updateEmail').attr('disabled', false);
          }
        );
    } else {
      this.error = true;
      jQuery('#updateEmail').html('Change Email');
      jQuery('#updateEmail').attr('disabled', false);
      this.Message = "Com'on atleast change something.";
    }

  }

  callGA(opt: string) {
    switch (opt) {
      case "UPDATEACCOUNT":
        ga('markettingteam.send', 'event', 'Settings', 'Click', 'EditInfo');
        // _kmq.push(['record', 'Settings Update account basic info click']);
        break;
    }
  }
  // Reset the form with a new hero AND restore 'pristine' class state
  // by toggling 'active' flag which causes the form
  // to be removed/re-added in a tick via NgIf
  // TODO: Workaround until NgForm has a reset method (#6822)
  upload(type = 'main') {
    const apikey = this.filePickerKey;
    const client = filestack.init(apikey);
    client.pick({
      storeTo: {
        location: 's3',
        access: 'public'
      },
      onFileSelected: function (file) {
        let fileName = file.filename;
        fileName = fileName.replace(/[^A-Za-z0-9.]/g, "_");
        fileName = fileName.replace(/ /g, "_");
        file.name = fileName;
        return file;
      },
      uploadInBackground: false,
      maxSize: 10485760,
      accept: 'image/*',
      // imageMax: [144, 42],
      // imageDim: [144, 42]
    }).then((result) => {
      let s3URL = 'https://dlvkyia8i4zmz.cloudfront.net/' + result.filesUploaded[0].key;
      if (type == 'main') {
        this._subDomainService.company_custom_features['extras']['company_logo']['link'] = s3URL || '';
        this._subDomainService.company_custom_features['extras']['company_logo']['fileName'] = result.filesUploaded[0].filename || '';
      } else {
        this._subDomainService.company_custom_features['extras']['company_logo']['builderLogo'] = s3URL || '';
        this._subDomainService.company_custom_features['extras']['company_logo']['builderfileName'] = result.filesUploaded[0].filename || '';
      }

      this._companyService.updateCustomFeatures({ customFeatures: this._subDomainService.company_custom_features, update: 'extras' })
        .subscribe(data => {
          type == 'main' && (this._subDomainService.companyLogo = s3URL || '');
          type == 'main' || (this._subDomainService.builderLogo = s3URL || '');

          this._subDomainService.company_custom_features = new CustomFeatures(data);
        }, error => {
          this._subDomainService.company_custom_features = new CustomFeatures(null);
        })
    });
  }
  removeLogo(type) {
    type == 'main' && this._subDomainService.removeLogo('link', 'fileName');
    type == 'builder' && this._subDomainService.removeLogo('builderLogo', 'builderfileName');
  }
}
