  updateBillingAddress() {
    const self = this;
    this.error = {status: false, message: null};
    jQuery('#btnBilling').html('please wait...').attr('disables', true);
    const updateBillingAddress = self.membershipService.setupBilling(this.billingAddressForm.value)
      .subscribe(
        (success: any) => {
          this.membershipService.setBillingDetail(success);
          jQuery('#btnBilling').html('Save').attr('disables', false);
          self.error = {status: false, message: null};
          window.toastNotification('Billing address updated successfully');
          updateBillingAddress.unsubscribe();
        },
        (error: any) => {
          self.error = {
            status: true,
            message: error.error.err_message
          };
          // const start = self.error.message.indexOf(':');
          // const end = self.error.message.length;
          // if (start !== -1) {
          //   this.error.message = this.error.message.substring(start + 1, end);
          // }
          console.log('updateBillingAddress error', self.error);
          jQuery('#btnBilling').html('Save').attr('disables', false);
          updateBillingAddress.unsubscribe();
        }
      );
  }

  initializeForm() {
    this.billingAddressForm = new FormGroup({
      name: new FormControl(this.customer ? this.customer.first_name : '', [Validators.required, Validators.minLength(3), Validators.pattern('^[a-zA-Z ]*$')]),
      line1: new FormControl(this.billingAddress.line1, [Validators.required, Validators.minLength(3), Validators.pattern('^[a-zA-Z0-9 ]*$')]),
      city: new FormControl(this.billingAddress.city, [Validators.required, Validators.minLength(3), Validators.pattern('^[a-zA-Z0-9 -]*$')]),
      state: new FormControl(this.billingAddress.state, [Validators.required, Validators.minLength(3), Validators.pattern('^[a-zA-Z0-9 -]*$')]),
      country: new FormControl(this.billingAddress.country, [Validators.required]),
      zip: new FormControl(this.billingAddress.zip, [Validators.required, Validators.minLength(4), Validators.pattern('^[a-zA-Z0-9]*$')])
    });
  }

  initCardDetails() {
    const self = this;
    this.subscribes.push(this.membershipService.getBillingDetail().subscribe((billingDetail) => {
      self.cardDetail = billingDetail.card;
      self.customer = billingDetail.customer;
      if (billingDetail.customer) {
        self.billingAddress = billingDetail.customer.billing_address;
        this.initializeForm();
      }
      if (jQuery.isEmptyObject(billingDetail)) {
        this.subscribes.push(self.getBillingDetail());
      } else {
        // this.loading.projects = false;
      }
    }));
  }

  getBillingDetail() {
    return this.membershipService.getBillingAddress().subscribe((success: any) => {
      this.membershipService.setBillingDetail(success);
    }, (error: any) => {
      console.error('getCardDetail error', error);
    });
  }
