import { IntercomService } from './../../../shared/services/intercom.service';
import { Component, OnInit, EventEmitter, ViewEncapsulation, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';
import { Company } from '../../../shared/models/company';
import { CompanyService } from '../../../shared/services/company.service';
import { environment } from './../../../../environments/environment';
import { SettingsCommunicationService } from '../../../shared/services/settings.communication.service';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { FeatureAuthService } from '../../../shared/services/feature-access.service';
import { SubDomainService } from '../../../shared/services/subdomain.service';
import { CookieService } from '../../../shared/services/cookie.service';
declare var jQuery: any;
declare var window: any;

@Component({
  selector: 'og-custom-domain',
  templateUrl: './customDomain.component.html',
  styleUrls: ['./customDomain.component.css']
})

export class CustomDomainComponent implements OnInit, AfterViewInit {
  myCompaniesList: any[];
  //subdomainExtension:string;
  currentSubdomain: string = '';
  currentCompanyCname: string = '';
  currentCompanyId: any;
  customDomain: string = '';
  rootUrl: string = '';
  errorUrl: string = '';
  formCname: FormGroup;
  CnameDetail: FormGroup;
  error: string;
  rootError: string;
  cloudflare = false;
  Company: any = new Company({});
  constructor(
    public _companyService: CompanyService,
    public fb: FormBuilder,
    public _featureAuthService: FeatureAuthService,
    private subdomainService: SubDomainService,
    private cookieService: CookieService,
    public _intercomService: IntercomService
  ) { }
  ngOnInit() {
    // let userRole = this.cookieService.readCookie('role');
    // if(userRole != 'ADMIN') {
    //   window.location.href = environment.PROTOCOL + this.subdomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/dashboard/';
    //   return;
    // }else {
    jQuery.material.init();
    //this.subdomainExtension = '.' + environment.APP_EXTENSION;
    this.formCname = this.fb.group({
      customDomain: [this.customDomain, Validators.compose([Validators.required, Validators.pattern('^([a-z0-9]+(-[a-z0-9]+)*\.)+[a-z]{2,}$')])],
    });
    this.CnameDetail = this.fb.group({
      rootUrl: [this.Company.cname.root_url, c => this.customValidator(c)],
      errorUrl: [this.Company.cname.errorUrl]
    });
    this.currentSubdomain = window.location.host.split('.')[0];
    this.getMyCompanies();
    // }
  }
  customValidator(controls) {
    if (controls.value === this.Company.cname.url) {
      return {
        matched: true
      };
    } else {
      return null;
    }


  }
  ngAfterViewInit() {
    const self = this;
    let interval = setInterval(function () {
      if (window.Intercom) {
        if (!self._featureAuthService.features.cname.active) {
          window.location.href = environment.PROTOCOL + self.subdomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/settings/';
        } else {
          window.Intercom('update', { 'app_current_page': 'custom-domain' });
          window.Intercom('update', { 'app_current_page_url': window.location.href });
        }
        clearInterval(interval);
      }
    }, 1000);
  }
  getMyCompanies() {
    let self = this;
    self.myCompaniesList = [];
    let getCompany = self._companyService.getCompanies()
      .subscribe(
        (success: any) => {
          success.forEach((company) => {
            self.currentSubdomain = window.location.host.split('.')[0];
            if (company.user_company.status != 'LEFT' && company.user_company.status != 'DELETED' && company.user_company.active) {
              self.myCompaniesList.push(new Company(company));
              if (self.currentSubdomain === company.sub_domain) {
                this.Company = new Company(company);
                self.currentCompanyId = company._id;
                self.currentCompanyCname = company.cname.url;
                self.rootUrl = company.cname.root_url;
                self.error = company.cname.errorUrl;
                self.cloudflare = company.cname.cloudflare;
                if ((self.rootUrl.length !== 0) || self.errorUrl.length !== 0) {
                  jQuery('#btnSaveDetails').html('Update');
                }
              }
            }
          });
        },
        (error: any) => {
          getCompany.unsubscribe();
        }
      );
  }

  verifyCustomDomain() {
    let self = this;
    this.error = '';
    self.currentSubdomain = window.location.host.split('.')[0];
    let data = {
      customDomain: this.formCname.value.customDomain,
      curSubDomain: self.currentSubdomain,
      cloudflare: this.cloudflare
    };
    if (this._featureAuthService.features.cname.active) {
      if (this.cloudflare) {
        jQuery('#btnSave').html('Please Wait...').attr('disabled', true);

      } else {
        jQuery('#btnVerify').html('Please Wait...').attr('disabled', true);
      }
      let verifyCustomDomain = self._companyService.verifyCustomDomain(data)
        .subscribe(
          (success: any) => {
            this.error = '';
            jQuery('#customDomain').val('');
            if (data.customDomain === null) {
              window.toastNotification('Changes Saved');
            } else if (this.cloudflare) {
              window.toastNotification('Custom domain applied');
            } else {
              window.toastNotification('Custom domain verified and applied');

            }
            if (this.cloudflare) {
              jQuery('#btnSave').html('Save').attr('disabled', false);

            } else {
              jQuery('#btnVerify').html('Verify').attr('disabled', false);
            }
            self.getMyCompanies();
          },
          (error: any) => {
            self.error = 'Domain does not exist or is invalid';
            jQuery('#btnVerify').html('Verify').attr('disabled', false);
            verifyCustomDomain.unsubscribe();
          }
        );
    } else {
      this._featureAuthService.setSelectedFeature('cname');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }
  saveCustomDetails() {
    let self = this;
    let data = this.CnameDetail.value;
    if (this._featureAuthService.features.cname.active) {
      if (data.rootUrl || data.errorUrl != '') {
        jQuery('#btnSaveDetails').html('Please Wait...').attr('disabled', true);
      }
      let cNameDetails = self._companyService.updateCompany(this.currentCompanyId, data)
        .subscribe(
          (success: any) => {
            this.error = '';
            self.getMyCompanies();
            if (data.rootUrl === '' && data.errorUrl === '') {
              jQuery('#btnSaveDetails').html('Submit').attr('disabled', false);
            } else if (data.rootUrl === '' || data.errorUrl === '') {
              jQuery('#btnSaveDetails').html('Submit').attr('disabled', false);
              window.toastNotification('Changes Saved');
            }
            else {
              window.toastNotification('Changes Saved');
              jQuery('#btnSaveDetails').html('Update').attr('disabled', false);
            }
          },
          (error: any) => {
            cNameDetails.unsubscribe();
          }
        );
    } else {
      this._featureAuthService.setSelectedFeature('cname');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }
  removecname() {
    this.formCname.controls['customDomain'].setValue(null);
    this.formCname.get('customDomain').markAsTouched();
    this.verifyCustomDomain();
  }
  openIntercom(message = '') {
    window.Intercom('showNewMessage', message);
  }
  // triggerClickToIntercom() {
  //   const intercomIframe = jQuery('iframe[name="intercom-messenger-frame"]').contents();
  //   intercomIframe.find('.intercom-composer-send-button').trigger('click');
  // }
  sendMessageToIntercom(message = '') {
    this.openIntercom(message);
    // setTimeout(() => this.triggerClickToIntercom(), 100);
    this._intercomService.triggerClickToIntercom();
  }
  cloudflareCheck(event) {
    this.cloudflare = event;
  }
}
