import {Component, OnInit,EventEmitter, ViewEncapsulation} from '@angular/core';
import {Router} from '@angular/router';
import {environment} from './../../../../environments/environment';
import { Company } from '../../../shared/models/company';
import {CompanyService} from '../../../shared/services/company.service';
import {SettingsCommunicationService} from '../../../shared/services/settings.communication.service';
import {SubDomainService} from '../../../shared/services/subdomain.service';
import {CookieService} from '../../../shared/services/cookie.service';
declare var jQuery:any;
declare var window: any;
declare var Clipboard: any;
declare var bootbox: any;

@Component({
	selector: 'og-api-setting',
	templateUrl: './apiKey.component.html',
	encapsulation: ViewEncapsulation.None,
	styleUrls: ['./apiKey.component.css','./../settings.component.css']
})

export class ApiKeyComponent implements OnInit {
	Message:any = '';
	companies:any = [];
	myCompaniesList:any;
	activeInCompanies:any;
	clickedCompany:any = new Company({});
	apiKey:string = '';
	loader = true;

	constructor(
		public _companyService: CompanyService,
		public router : Router,
		public settingsCommunicationService:SettingsCommunicationService,
		private subdomainService: SubDomainService,
    	private cookieService: CookieService
		){}

	ngOnInit() {
		// let userRole = this.cookieService.readCookie('role');
		// if(userRole != 'ADMIN') {
		// 	window.location.href = environment.PROTOCOL + this.subdomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/dashboard/';
		// 	return;
		// } else {
			this.getMyCompanies();
			if (window.Intercom) {
				window.Intercom('update', { 'app_current_page': 'api-key' });
				window.Intercom('update', { 'app_current_page_url': window.location.href });
			}
		// }
	}

	getMyCompanies(){
		let self = this;
		let getCompany = self._companyService.getCompanies()
			.subscribe(
				(success:any)=>{
					self.myCompaniesList = [];
					self.activeInCompanies = [];
					const simpleCompanyList = []
					success.forEach((company:any,index:any)=>{
						if(company.user_company.status != 'LEFT' && company.user_company.status != 'DELETED'){
							self.myCompaniesList.push(new Company(company));
							if(company.user_company.active){
								self.activeInCompanies.push(new Company(company));
								simpleCompanyList.push(company);
							}
							company.domain = company.sub_domain + '.' +environment.APP_EXTENSION;
							self.companies.push(company);
							//console.log(self.companies);
						if(company.api == null)
							self.generateApiKey(company._id,index,'Generated');
						}
						this.loader = false;
					});
					self.settingsCommunicationService.updateCompanyList(self.myCompaniesList, simpleCompanyList);
					// self.myCompaniesListUpdated.emit(self.myCompaniesList);
				},
				(error:any)=>{
					getCompany.unsubscribe();
				}
			);
	}

	generateApiKey(compId:string,index:any,scope:string='Regenerated'){
		let self = this;
		jQuery('#regenerate-api-'+index+' span').html('Please wait');
		let generateApiKey = self._companyService.generateApiKey(compId)
			.subscribe(
				(success:any)=> {
					if(scope !== 'Generated')
						window.toastNotification('API Key '+ scope);
					jQuery('#regenerate-api-'+index).addClass('hide');
					self.companies[index].api = success.api;
					jQuery('#regenerate-api-'+index).html('Regenerate');
				},
				(error:any)=> {
					self.Message = error.error.message;
					window.toastNotification(self.Message);
					generateApiKey.unsubscribe();
					// jQuery('#regenerate-api-'+index).html('Regenerate');
				}
			);
	}
	copyApi(index:string){
		// clipboard.copy(jQuery('#input-api-'+index)[0].innerText)
		new Clipboard('.cpy-api', {
			text: function(trigger) {
				return jQuery('#input-api-'+index)[0].innerText;
			}
		});
		window.toastNotification('API Key Copied');
	}
}
