ius: 0px 17px 17px 0px;\n  }\n\n.billing-plan-list ul > li:nth-child(3) > a {\n    color: #8e989f;\n    font-size: 11px;\n    text-transform: uppercase;\n    border: none !important;\n    border-left: 1px solid #d7dbdd !important;\n    -webkit-border-top-right-radius: 25px;\n    -webkit-border-bottom-right-radius: 25px;\n    -moz-border-radius-topright: 25px;\n    -moz-border-radius-bottomright: 25px;\n    border-top-right-radius: 25px;\n    border-bottom-right-radius: 25px\n  }\n\n.billing-plan-list ul > li:nth-child(3) > a.hvr-sweep-to-right::before {\n    background: #f6f8f9;\n    border-top-right-radius: 25px;\n    border-bottom-right-radius: 25px\n  }\n\n#pricing .billing-plan-list ul > li > a {\n    padding: 8px 0;\n  }\n\n.billing-plan-list ul > li a i.material-icons {\n    font-size: 18px;\n    color: #62696d;\n    vertical-align: middle;\n    padding-right: 3px;\n    display: none;\n  }\n\n.billing-plan-list ul > li.active a i.material-icons {\n    font-size: 18px;\n    color: #0ead48;\n    vertical-align: middle;\n    padding-right: 3px;\n    display: inline-block;\n  }\n\n/* end: billing plan list css */\n\n/* table */\n\n.pricing-outer table {\n  width: 100%;\n  text-align: left;\n  border-spacing: 0;\n  border-collapse: collapse;\n  box-sizing: border-box;\n}\n\n.pricing-outer th, .pricing-outer td {\n  font-family: Roboto-Regular;\n  font-size: 12px;\n  /* line-height: 1.45; */\n  color: #666;\n  vertical-align: top;\n  /*padding: 10px;*/\n}\n\n.pricing-outer th {\n  font-weight: normal;\n}\n\n.pricing-outer colgroup:nth-child(1) {\n  width: 25%;\n  border: 0 none;\n}\n\n.pricing-outer colgroup:nth-child(2), .pricing-outer colgroup:nth-child(3) {\n  width: 25%;\n  border: 1px solid #eff0f1;\n  background: #fff;\n}\n\n.pricing-outer colgroup:nth-child(4) {\n  width: 25%;\n  border: 1px solid #bed4df;\n  box-shadow: 0 5px 10px rgba(0, 0, 0, .10);\n  right: 0px;\n  position: absolute;\n  top: 0;\n  height: 100%;\n  z-index: 1;\n  /*border-top: none;*/\n\n