import { CompanyService } from './../../../shared/services/company.service';
import { SubDomainService } from './../../../shared/services/subdomain.service';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
declare var window:any;
@Component({
  selector: 'app-gdpr',
  templateUrl: './gdpr.component.html',
  styleUrls: ['./gdpr.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class GdprComponent implements OnInit {

  constructor(public _subDomainService:SubDomainService,
  public _companyService:CompanyService) { }

  ngOnInit() {
  }
  
  saveCompanyChanges(change){
    let changes = Object.assign({},this._subDomainService.currentCompany,change);    
    let $ref = this._companyService.updateCompany(this._subDomainService.currentCompany.id,changes)
    .subscribe(data=>{
      Object.assign(this._subDomainService.currentCompany,change); 
      console.log(this._subDomainService.currentCompany);
      window.toastNotification('Status Updated'); 
      $ref.unsubscribe();     
    },error=>{
      $ref.unsubscribe();
      console.log(error.error.err_message);
    });
  }
}
