import { Component, ViewEncapsulation, AfterViewInit, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {SettingsCommunicationService} from '../../../shared/services/settings.communication.service';
import {SubDomainService} from '../../../shared/services/subdomain.service';
import {CookieService} from '../../../shared/services/cookie.service';
import { environment } from './../../../../environments/environment';
declare var window: any;
declare var jQuery:any;
@Component({
  selector: 'og-account',
  templateUrl: './account.component.html',
  encapsulation: ViewEncapsulation.None,
	styleUrls: ['./account.component.css','./custom-material.css','./../settings.component.css']
})

export class AccountComponent implements OnInit, AfterViewInit{

  myCompanies:any;
  storage: any;
  forceSetup: boolean;
  constructor(public router: Router,
    public settingsCommunicationService : SettingsCommunicationService,
    public subdomainService: SubDomainService,
    private cookieService: CookieService) {
    this.myCompanies = this.settingsCommunicationService.companyList;
    jQuery.material.init();
    this.forceSetup = false;
  }

  ngOnInit() {
    // let userRole = this.cookieService.readCookie('role');
    // if(userRole != 'ADMIN') {
    //   window.location.href = environment.PROTOCOL + this.subdomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/dashboard/';
    //   return;
    // }
  }
  ngAfterViewInit() {
    let interval = setInterval(function(){
      if(window.Intercom){
        window.Intercom('update', { 'app_current_page': 'my-account' });
      window.Intercom('update', { 'app_current_page_url': window.location.href });
        clearInterval(interval);
      }
    },1000);
  }
}
