  getMyCompanies() {
    let self = this;
    self._companyService.getCompanies().subscribe(
      (success: any) => {
        self.myCompaniesList = [];
        success.forEach((company: any) => {
          if (
            company.user_company.status !== "DELETED" &&
            company.user_company.status !== "LEFT"
          )
            self.myCompaniesList.push(new Company(company));
        });
      },
      (error: any) => {
        //console.log(error);
      }
    );
  }
  getCompany() {
    this.isAdmin = false;
    let self = this;
    jQuery("#actuser").addClass("active");
    jQuery("#invuser").removeClass("active");
    jQuery("#accreq").removeClass("active");
    jQuery("#active-users").addClass("active");
    jQuery("#invited-users").removeClass("active");
    jQuery("#access-requests").removeClass("active");
    this.selectedCompany = window.location.href.split("//")[1].split(".")[0];
    if (this.selectedCompany !== "app") {
      let subDomainExist = this._companyService
        .getCompanyBySubdomain(this.selectedCompany)
        .subscribe(
          (success: any) => {
            self.currentCompany = self._subDomainService.currentCompany = new CurrentCompany(
              success
            );
            this._subDomainService.subDomain.sub_domain = this.currentCompany.sub_domain;
            this._subDomainService.subDomain.company_id = this.currentCompany.id;
            this._subDomainService.subDomain.name = this.currentCompany.name;
            self.getSelectedCompanyUsers();
          },
          (error: any) => {
            subDomainExist.unsubscribe();
          }
        );
    }
  }

  getSelectedCompanyUsers() {
    this.isAdmin = false;
    this.loading = true;
    // setTimeout(function() {
    let self = this;
    let getCompanyUsers = this._companyService
      .getCompanyUsers(self.currentCompany.id)
      .subscribe(
        (users: any) => {
          self.hasRequest = false;
          self.currentCompanyUsers = [];
          self.adminCount = 0;
          var userLength = users.length;
          // console.log("users length", userLength,users);
          for (var i = 0; i < userLength; i++) {
            if (users[i].username !== self.logedInUserName)
              self.currentCompanyUsers.push(new User(users[i]));
            else {
              self.currentCompanyUsers.unshift(new User(users[i]));
              if (users[i].user_company.role === "ADMIN") self.isAdmin = true;
            }
            if (
              self.adminCount < 1 &&
              users[i].user_company.role === "ADMIN" &&
              users[i].username !== self.logedInUserName &&
              users[i].user_company.active
            ) {
              self.adminCount++;
            }
            if (users[i].user_company.status === "REQUESTED")
              self.hasRequest = true;
          }
          this.loading = false;
          if (this.toggleToInvited) {
            setTimeout(function () {
              const invuser = document.getElementById("invuser");
              invuser.classList.remove("active");
              const actuser = document.getElementById("actuser");
              actuser.classList.add("active");
            }, 0);
            this.toggleToInvited = false;
          }
          //});
          this.loading = false;
          // console.log('currentCompanyUserscurrentCompanyUsers',self.currentCompanyUsers);
          this.route.queryParams.subscribe((params: Params) => {
            let data = params["userInvite"];
            console.log("datadatadatadatadatadatadata ", data);
            if (data) {
              setTimeout(() => {
                this.userCheckLimit();
              }, 1000);
            }
          });
        },
        (error: any) => {
          getCompanyUsers.unsubscribe();
        }
      );
  }
