  userCheckLimit() {
    this.edit = false;

    //console.log(this.currentCompanyUsers.length ,'of', this._featureAuthService.features.users);
    if (
      this._featureAuthService.features.users === -1 ||
      this.currentCompanyUsers.length < this._featureAuthService.features.users
    ) {
      jQuery("#add-new-user").modal("show");
      jQuery(".modal-backdrop").insertAfter("#premiumModal");
      jQuery("#premiumModal").attr("active", false);
      this.callGA("ADDUSERCLICK");
      this.resetForm();
    } else {
      this._featureAuthService.setSelectedFeature("Team Getting Bigger?");
      //jQuery('.users').addClass('activegreen limited-label');
      jQuery("#premiumModal").modal("show");
      jQuery("#add-new-user").attr("active", false);
    }
  }

