import { AfterViewInit, Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { CompanyService } from '../../../shared/services/company.service';
import { environment } from '../../../../environments/environment';
import { SettingsCommunicationService } from '../../../shared/services/settings.communication.service';
import { SubDomainService } from '../../../shared/services/subdomain.service';
import { Company } from '../../../shared/models/company';
import { CookieService } from '../../../shared/services/cookie.service';
import { UserService } from '../../../shared/services/user.service';
import { LoggedInService } from '../../../shared/services/logged-in.service';
import { FeatureAuthService } from '../../../shared/services/feature-access.service';
import { Router } from '@angular/router';
declare let jQuery: any;
declare let window: any;
declare let Clipboard: any;
declare var bootbox: any;
declare var moment: any;

@Component({
  selector: 'og-companies',
  templateUrl: './companies.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./companies.component.css']
})
export class CompaniesComponent implements OnInit, OnDestroy, AfterViewInit {
  subscribes: any[];
  loadingCompanies: boolean;
  subdomainExtension: string;
  companyLists: any[];
  can_create_new_company: boolean;
  currentCompany: any;
  clickedCompany: any;
  leaveComp: any;
  updateCompany: any;
  EditCompany: Company = new Company({});
  error: any;
  updateCompanyForm: FormGroup;
  createCompanyForm: FormGroup;
  logedInUserName: string;
  activeInCompanies: Company[];
  whPlan = ['outgrow-whitelabel-10c_d',
    'outgrow-whitelabel-30c_d',
    'outgrow-whitelabel-100c_d'];
  constructor(private companyService: CompanyService,
    private subDomainService: SubDomainService,
    private cookieService: CookieService,
    private userService: UserService,
    private loggedInService: LoggedInService,
    private settingsCommunicationService: SettingsCommunicationService,
    private _featureAuthService: FeatureAuthService,
    private router: Router) {
    this.subscribes = [];
    this.loadingCompanies = false;
    this.companyLists = [];
    this.can_create_new_company = false;
    this.currentCompany = null;
    this.clickedCompany = null;
    this.logedInUserName = null;
    this.activeInCompanies = [];
    this.error = {
      status: false,
      message: ''
    };
  }

  ngOnInit() {
    // let userRole = this.cookieService.readCookie('role');
    // if(userRole != 'ADMIN') {
    //   window.location.href = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/dashboard/';
    //   return;
    // } else {
    this.subscribes.push(this.initCompanies());
    this.subdomainExtension = '.' + environment.APP_EXTENSION;
    this.currentCompany = this.subDomainService.currentCompany;
    this.initUpdateCompanyForm();
    this.initCreateCompanyForm();
    const cookie = this.cookieService.readCookie('storage');
    if (cookie !== null) {
      const storage = JSON.parse(cookie);
      this.logedInUserName = storage.user.username;
    }
    // }
  }

  ngOnDestroy() {
    this.subscribes.forEach((subscribe) => {
      subscribe.unsubscribe();
    });
  }

  ngAfterViewInit() {
    const self = this;
    let interval = setInterval(function () {
      if (window.Intercom) {
        window.Intercom('update', { 'app_current_page': 'companies' });
        window.Intercom('update', { 'app_current_page_url': window.location.href });
        clearInterval(interval);
      }
    }, 1000);
    let newInterval = setInterval(() => {
      if (self.currentCompany && self.currentCompany.agency !== undefined) {
        self.can_create_new_company = (self.currentCompany.agency && !self.currentCompany.parent_company && self.currentCompany.current_limit.companies) ? true : false;
        clearInterval(newInterval);
      } else {
        self.currentCompany = self.subDomainService.currentCompany;
      }
    }, 1000);
  }

  initCompanies() {
    this.loadingCompanies = true;
    if (!this.companyLists.length) {
      return this.companyService.getCompanies().subscribe(
        (companies: any) => {
          this.settingsCommunicationService.updateCompanyList(companies);
          this.companyLists = this.settingsCommunicationService.companyList;
          this.activeInCompanies = [];
          companies.forEach((company: any) => {
            if (company.user_company.status !== 'LEFT' && company.user_company.status !== 'DELETED' && company.user_company.active) {
              this.activeInCompanies.push(new Company(company));
            }
          });
          this.loadingCompanies = false;
        }, (error: any) => {
          console.error('initCompanies error', error);
          this.loadingCompanies = false;
        });
    } else {
      this.companyLists = this.settingsCommunicationService.companyList;
      this.loadingCompanies = false;
    }
  }

  showDetails(company: any) {
    this.clickedCompany = company;
    jQuery('#companyLists').addClass('hide');
    jQuery('#companyDetails').removeClass('hide');
  }

  backToList() {
    jQuery('#companyDetails').addClass('hide');
    jQuery('#companyLists').removeClass('hide');
  }

  copyApi() {
    // clipboard.copy(jQuery('#input-api')[0].value)
    new Clipboard('.cpy-api2', {
      text: function (trigger) {
        return jQuery('#input-api')[0].value;
      }
    });
    window.toastNotification('API Copied');
  }

  generateApiKey(compId: string) {
    const self = this;
    const generateApiKey = self.companyService.generateApiKey(compId)
      .subscribe(
        (success: any) => {
          this.clickedCompany = new Company(success);
          this.subscribes.push(generateApiKey);
        },
        (error: any) => {
          this.error = {
            status: true,
            message: error.error.message
          };
          window.toastNotification(this.error.message);
          generateApiKey.unsubscribe();
        }
      );
  }

  popupUpdateCompany(company: any) {
    this.updateCompany = new Company(company);
    this.EditCompany = new Company(company);
    this.initUpdateCompanyForm();
    const cdomain: any = document.getElementById('updDomain');
    cdomain.value = this.EditCompany.sub_domain;
    if (this.EditCompany.name === '' || this.EditCompany.name === null) {
      jQuery('#updCompanynameDiv').addClass('is-empty');
    } else {
      jQuery('#updCompanynameDiv').removeClass('is-empty');
    }
    if (!this.EditCompany.sub_domain) {
      jQuery('#updDomainDiv').addClass('is-empty');
    } else {
      jQuery('#updDomainDiv').removeClass('is-empty');
    }
    if (!this.EditCompany.traffic.frequency) {
      jQuery('#updTrafficDiv').addClass('is-empty');
    } else {
      jQuery('#updTrafficDiv').removeClass('is-empty');
    }
    if (!this.EditCompany.leads.total) {
      jQuery('#updLeadsDiv').addClass('is-empty');
    } else {
      jQuery('#updLeadsDiv').removeClass('is-empty');
    }
  }

  updateThisCompany() {
    jQuery('#btnUpdateCompany').text('Please wait...');
    jQuery('#btnUpdateCompany').attr('disabled', true);
    this.error = {
      status: false,
      message: ''
    };
    const updateThisCompany = this.companyService.updateCompany(this.updateCompany.id, this.updateCompanyForm.value)
      .subscribe(
        (success: any) => {
          this.companyLists = [];
          this.initCompanies();
          jQuery('#btnUpdateCompany').text('Update');
          jQuery('#btnUpdateCompany').attr('disabled', false);
          jQuery('#change-company-name').modal('hide');
          jQuery('#change-company-name input').val('');
          jQuery('#change-company-name div.label-floating').addClass('is-empty');
          window.toastNotification('Company Updated Successfully');
          jQuery('#btnUpdateCompany').text('Update');
          this.initUpdateCompanyForm();
          const curUrlDomain = window.location.hostname.split('.')[0];
          let redirectDomain = '';
          const filepicker_token_json = JSON.parse(this.cookieService.readCookie('filepicker_token_json'));
          filepicker_token_json.forEach((key: any) => {
            if (key.key === curUrlDomain) {
              key.key = success.sub_domain;
            }
          });
          const storage = JSON.parse(this.cookieService.readCookie('storage'));
          let compList = storage.companyList;
          const index = compList.indexOf(curUrlDomain);
          if (index !== -1) {
            compList[index] = success.sub_domain;
          }
          console.log('compList', compList);
          storage.companyList = compList;
          this.cookieService.createCookie('filepicker_token_json', JSON.stringify(filepicker_token_json), 3);
          this.cookieService.createCookie('storage', JSON.stringify(storage), 3);
          if (curUrlDomain !== success.sub_domain && curUrlDomain === this.updateCompany.sub_domain) {
            redirectDomain = success.sub_domain + this.subdomainExtension;
            storage.currentCompany = success.sub_domain;
            this.cookieService.createCookie('storage', JSON.stringify(storage), 3);
            jQuery(location).attr('href', environment.PROTOCOL + redirectDomain + '/settings/companies');
          }
        },
        (error: any) => {
          jQuery('#btnUpdateCompany').text('Update');
          jQuery('#btnUpdateCompany').attr('disabled', false);
          this.error.status = true;
          if (error.error.code === 'E_UNIQUE_UNIDENTIFIED_VALIDATION') {
            this.error.message = 'sub-domain already taken';
          } else if (error.error.err_errors !== '') {
            this.error.message = error.error.err_errors.sub_domain.message;
          } else {
            this.error.message = error.error.err_message;
          }
          jQuery('#success-updateCompany').removeClass('hide');
          updateThisCompany.unsubscribe();
        }
      );
  }

  initUpdateCompanyForm() {
    this.error = {
      status: false,
      message: ''
    };
    this.updateCompanyForm = new FormGroup({
      companyname: new FormControl(this.EditCompany.name, [Validators.required, Validators.minLength(3)]),
      domain: new FormControl(this.EditCompany.sub_domain, [Validators.required, Validators.minLength(4), Validators.pattern('^[a-zA-Z0-9]*$')])
    });
  }

  initCreateCompanyForm() {
    this.error = {
      status: false,
      message: ''
    };
    this.createCompanyForm = new FormGroup({
      companyname: new FormControl('', [Validators.required, Validators.minLength(3)]),
      domain: new FormControl('', [Validators.required, Validators.minLength(4), Validators.pattern('^[a-zA-Z0-9]*$')]),
      agency: new FormControl(false),
      parent_company: new FormControl(this.subDomainService.subDomain.sub_domain)
    });
  }

  leaveCompany(company) {
    this.leaveComp = company;
    let adminCount = 0;
    const leaveCompany = this.companyService.getCompanyUsers(company._id)
      .subscribe(
        (success: any) => {
          success.forEach((user: any) => {
            if (adminCount < 1 && user.user_company.role === 'ADMIN' && user.user_company.active && user.username !== this.logedInUserName) {
              adminCount++;
            }
          });
          if (adminCount >= 1) {
            if (this.activeInCompanies.length > 1) {
              this.leaveBootbox();
              // jQuery('#confirmation').modal('show');
            } else {
              this.leaveComp = company;
              this.leaveCompanyBootbox();
              // jQuery('#leaveConfirmation').modal('show');
            }
          } else {
            window.toastNotification('The company must have another admin!');
          }
        },
        (error: any) => {
          this.leaveOnConfirm();
        }
      );
  }
  leaveCompanyBootbox() {
    const text = ' <div class="bootbox-heading">Are you sure you wish to leave this company?</div><div class="bootbox-subheading">This is the only company you belong to. If you leave this company, your account will be deleted.</div>';
    bootbox.dialog({
      message: `
                <button type="button" class="bootbox-close-button close b-close" data-dismiss="modal" aria-hidden="true">
                <i class='material-icons'>close</i></button>
             <div class="bootbox-body-left extra-mar">
                    <div class="mat-icon">
                      <i class="material-icons">error</i>
                    </div>
                </div>
                <div class="bootbox-body-right full-width">
                  ${text}
                </div>
            `,
      buttons: {
        cancel: {
          label: "NO",
          className: "btn btn-cancel btn-cancel-hover"
        },
        success: {
          label: 'YES',
          className: "btn btn btn-ok btn-hover new-del-btn",
          callback: () => {
            this.deleteMe();
          }
        },
      }
    });
  }

  leaveBootbox() {
    const text = ' <div class="bootbox-heading">Are you sure you wish to leave this company?</div>';
    bootbox.dialog({
      message: `
                <button type="button" class="bootbox-close-button close b-close" data-dismiss="modal" aria-hidden="true">
                <i class='material-icons'>close</i></button>
             <div class="bootbox-body-left extra-mar">
                    <div class="mat-icon">
                      <i class="material-icons">error</i>
                    </div>
                </div>
                <div class="bootbox-body-right full-width">
                  ${text}
                </div>
            `,
      buttons: {
        cancel: {
          label: "NO",
          className: "btn btn-cancel btn-cancel-hover"
        },
        success: {
          label: 'YES',
          className: "btn btn btn-ok btn-hover new-del-btn",
          callback: () => {
            this.leaveOnConfirm();
          }
        },
      }
    });
  }
  acceptInvite(company: any) {
    this.error = {
      status: false,
      message: ''
    };
    const userApproval = this.userService.userApproval(company.sub_domain).subscribe(
      (success: any) => {
        let filepicker_token_json = JSON.parse(this.cookieService.readCookie('filepicker_token_json'));
        filepicker_token_json.push({ key: company.sub_domain, value: company.billing.chargebee_subscription_status });
        this.cookieService.createCookie('filepicker_token_json', JSON.stringify(filepicker_token_json), 3);
        const storage = JSON.parse(this.cookieService.readCookie('storage'));
        storage.companyList.push(company.sub_domain);
        this.cookieService.createCookie('storage', JSON.stringify(storage), 3);
        jQuery('.comp' + company.id).attr('checked', true);
        window.toastNotification('Successfully accepted Company request');
        window.location.assign(environment.PROTOCOL + company.sub_domain + this.subdomainExtension + '/dashboard');
      },
      (error: any) => {
        window.toastNotification(error.error.err_message);
        userApproval.unsubscribe();
      }
    );
  }

  deleteMe() {
    // jQuery('#leaveConfirmation').modal('hide');
    const deleteCompany = this.companyService.leaveCompany(this.leaveComp._id)
      .subscribe(
        (success: any) => {
          window.toastNotification('Company Left Successfully');
          // if (this.userService.logout()) {
          //   this.loggedInService.logout();
          //   window.location.assign(environment.PROTOCOL + environment.PARENT_APP_DOMAIN);
          // }
          this.router.navigate(['/logout']);
        },
        (error: any) => {
          window.toastNotification(error.error.message);
          deleteCompany.unsubscribe();
        }
      );
  }

  leaveOnConfirm() {
    const leaveOnConfirm = this.companyService.leaveCompany(this.leaveComp._id)
      .subscribe(
        (success: any) => {
          const filepicker_token_json = JSON.parse(this.cookieService.readCookie('filepicker_token_json'));
          const storage = JSON.parse(this.cookieService.readCookie('storage'));
          let i = 0;
          for (i = 0; i < storage.companyList.length; i++) {
            if (storage.companyList[i] === this.leaveComp.sub_domain) {
              break;
            }
          }
          storage.companyList.splice(i, 1);
          this.cookieService.createCookie('storage', JSON.stringify(storage), 3);
          filepicker_token_json.splice(i, 1);
          this.cookieService.createCookie('filepicker_token_json', JSON.stringify(filepicker_token_json), 3);
          window.toastNotification('Successfully left ' + this.leaveComp.name);
          const curUrlDomain = window.location.hostname.split('.')[0];
          let redirectDomain = 'app.outgrow' + this.subdomainExtension;
          if (curUrlDomain === this.leaveComp.sub_domain) {
            for (let i = 0; i < this.companyLists.length; i++) {
              if (curUrlDomain !== this.companyLists[i].sub_domain && this.companyLists[i].user_company.active) {
                redirectDomain = this.companyLists[i].sub_domain + this.subdomainExtension + '/settings/companies';
                storage.currentCompany = this.companyLists[i].sub_domain;
                this.cookieService.createCookie('storage', JSON.stringify(storage), 3);
                break;
              }
            }
            console.log('redirectDomainredirectDomain', redirectDomain);
            jQuery(location).attr('href', environment.PROTOCOL + redirectDomain);
          }
          this.companyLists = [];
          this.initCompanies();
        },
        (error: any) => {
          window.toastNotification(error.error.err_message);
          leaveOnConfirm.unsubscribe();
        }
      );
  }

  createCompany() {
    this.error = {
      status: false,
      message: ''
    };
    if (this.currentCompany.agency) {
      jQuery('#btnCreateCompany').text('Please Wait...').attr('disabled', true);
      const createCompany = this.companyService.createCompany(this.createCompanyForm.value).subscribe(
        (success: any) => {
          const storage = JSON.parse(this.cookieService.readCookie('storage'));
          storage.companyList.push(success.sub_domain);
          this.cookieService.createCookie('storage', JSON.stringify(storage), 3);
          jQuery('#btnCreateCompany').text('Add New Company');
          this.initCreateCompanyForm();
          window.toastNotification('Company Created Successfully!');
          jQuery('#add-new-company').modal('hide');
          const cp = JSON.parse(this.cookieService.readCookie('filepicker_token_json'));
          cp.push({ 'key': success.sub_domain, 'value': this.currentCompany.billing.chargebee_subscription_status });
          this.cookieService.createCookie('filepicker_token_json', JSON.stringify(cp), 3);
          window.location.reload();
          // window.location.href = environment.PROTOCOL + success.sub_domain + '.' + environment.APP_EXTENSION + '/dashboard';
        },
        (error: any) => {
          this.error.status = true;
          if (error.code === 'E_UNIQUE_UNIDENTIFIED_VALIDATION') {
            this.error.message = 'sub-domain already taken';
          } else if (error.error.err_errors !== '') {
            this.error.message = error.error.err_errors.sub_domain.message;
          } else {
            this.error.message = error.error.err_message;
          }
          jQuery('#success-addCompany').removeClass('hide');
          jQuery('#btnCreateCompany').text('Add New Company');
          jQuery('#btnCreateCompany').attr('disabled', false);
          createCompany.unsubscribe();
        });
    } else {
      this.error = {
        status: true,
        message: 'You must be a agency to add company'
      };
    }
  }

  addCompanyCheck() {
    const createdDate = moment(this.currentCompany.createdAt);
    const companyRestrictDate = moment(new Date('2018/03/27'));
    const restrictDif = companyRestrictDate.diff(createdDate, 'days');
    let limit = this.currentCompany.current_limit.companies;
    if (restrictDif > 0 && limit > 0)
      limit += 1;
    if (limit <= this.companyLists.length) {
      this.openBootbox();
    } else {
      jQuery('#add-new-company').modal('show');
    }
  }

  openBootbox() {
    this._featureAuthService.setSelectedFeature('Companies');
    jQuery('#premiumModal').modal('show');
    jQuery('#add-new-user').attr('active', false);
    // bootbox.dialog({
    //   size: 'small',
    //   message: `<div class="bootbox-body-left">
    //               <div class="mat-icon"><i class="material-icons">error</i></div>
    //             </div>
    //             <div class="bootbox-body-right">
    //               <p class="">Sorry, You have exceeded the number of companies that you can create.
    //               Please contact us to add more companies</p>
    //             </div>`,
    //   buttons: {
    //     success: {
    //       label: 'Contact us',
    //       className: 'btn btn-ok btn-hover',
    //       callback: function () {
    //         jQuery('.intercom_trigger').trigger( "click" );
    //       }
    //     },
    //     cancel: {
    //       label: 'Cancel',
    //       className: 'btn-cancel btn-cancel-hover',
    //       callback: function () {
    //       }
    //     },
    //   }
    // });
  }
}
